import { Accordion, AccordionTab } from "primereact/accordion";
import { Column } from "primereact/column";
import { DataTable } from "primereact/datatable";
import { ProgressSpinner } from "primereact/progressspinner";
import * as React from "react";
import { getKeys, map } from "../../../model/types/Utils";
import { actions } from "../../../redux/CMTStore";
import * as Hooks from "../../../redux/hooks";
import { HARMONY_SUMMARY_LINK } from "../../common/common";
import { getState } from "../wizardSlice";
import type { ContentTypes } from "../../types";
import type { FeaturedContent } from "../wizardSlice";
import type { ContentBundle } from "../../types";
import type { Meta } from "../../../model/BaseContent/Manifest";

interface TableBundle {
    libraryName?: string;
    description?: string;
    melody: JSX.Element;
    classic: JSX.Element;
}
export type Props = {
    catalogData: ContentBundle<Meta[], Meta[], Meta[], Meta[]>;
};
export const LibrariesAccordian: React.FC<Props> = (props): JSX.Element => {
    const dispatch = Hooks.useAppDispatch();
    const state = getState(Hooks.useAppSelector((state) => state));

    const [hasRequested, setHasRequested] = React.useState(false);

    const AvailableNowText = (): JSX.Element => (
        <span className="text-success">
            <i className="pi pi-check me-2" /> Available Now
        </span>
    );
    const ComingSoonText = (): JSX.Element => (
        <span className="text-secondary">
            <i className="pi pi-clock me-2" /> Coming Soon
        </span>
    );
    const NotSupportedText = (): JSX.Element => (
        <span className="text-danger">
            <i className="pi pi-times me-2" /> Not Supported
        </span>
    );
    const isComingSoon = (libraryName: string, contentType: ContentTypes): boolean => {
        const catalogData = props.catalogData[contentType];

        catalogData.forEach((entry) => {
            if (entry.name === libraryName) {
                const comingSoon = entry?.isComingSoon ?? false;
                return comingSoon;
            }
        });

        return false;
    };

    const getMetaTableText = (
        libraryName: string,
        contentType: ContentTypes,
        supportedTypes: ContentTypes[],
    ): JSX.Element => {
        if (supportedTypes.includes(contentType)) {
            return isComingSoon(libraryName, contentType) ? (
                <ComingSoonText />
            ) : (
                <AvailableNowText />
            );
        }

        return <NotSupportedText />;
    };

    const translateMetaToTableBundle = (
        featuredContent: FeaturedContent,
    ): TableBundle[] => {
        const tableBundle: TableBundle[] = map(
            featuredContent,
            (name, supportedTypes) => {
                return {
                    libraryName: name,
                    description: "",
                    classic: getMetaTableText(name, "CLASSIC", supportedTypes),
                    melody: getMetaTableText(name, "MELODY", supportedTypes),
                };
            },
        );
        return tableBundle.sort((a, b) => {
            return a.libraryName?.localeCompare(b.libraryName ?? "") ?? 0;
        });
    };

    return (
        <div className={"row"}>
            <div className={"col-md-8"}>
                <Accordion
                    onTabOpen={(): void => {
                        if (!hasRequested) {
                            dispatch(actions.requestFeaturedContent());
                            dispatch(actions.requestCatalogData());
                            setHasRequested(true);
                        }
                    }}
                >
                    <AccordionTab header={"MCC Melody and MCC Classic - Library Summary"}>
                        {getKeys(state.featuredContent ?? {}).length > 0 ? (
                            <DataTable
                                value={translateMetaToTableBundle(
                                    state.featuredContent ?? {},
                                )}
                            >
                                <Column
                                    field="libraryName"
                                    header="Library Name"
                                    sortable
                                />
                                <Column field="melody" header="Melody" sortable />
                                <Column field="classic" header="Classic" sortable />
                            </DataTable>
                        ) : (
                            <div className={"centered"}>
                                <ProgressSpinner />
                            </div>
                        )}
                    </AccordionTab>
                </Accordion>
            </div>
            <div className={"col-md-4"}>
                <Accordion>
                    <AccordionTab header={"MPLAB Harmony - Library Summary"}>
                        <span
                            className={"text-link"}
                            onClick={(e): void => {
                                e.stopPropagation();
                                window.open(HARMONY_SUMMARY_LINK, "_blank");
                            }}
                        >
                            Click here to get to library summary of MPLAB Harmony
                        </span>
                    </AccordionTab>
                </Accordion>
            </div>
        </div>
    );
};
