import type { ClassicContentManifests } from "../model/classic/ClassicManifest";
import type { HarmonyContentManifests } from "../model/harmony/HarmonyManifest";
import type { MelodyContentManifests } from "../model/melody/MelodyManifest";
import type { TridionContentManifests } from "../model/tridion/TridionManifest";
import type { ContentId } from "../model/types/ContentId";

export type ContentBundle<T, U, V, Z> = {
    MELODY: T;
    CLASSIC: U;
    HARMONY: V;
    TRIDION: Z;
};

export type ContentList = {
    MELODY: MelodyContentManifests;
    CLASSIC: ClassicContentManifests;
    HARMONY: HarmonyContentManifests;
    TRIDION: TridionContentManifests;
};

export type ContentIdBundle = ContentBundle<
    ContentId[],
    ContentId[],
    ContentId[],
    ContentId[]
>;
export const EMPTY_CONTENT: ContentIdBundle = {
    MELODY: [],
    CLASSIC: [],
    HARMONY: [],
    TRIDION: [],
};

export type ContentConnectionStatus = {
    hasConnection: boolean;
    local: string;
    remote: string;
};

export type ContentListConnectionStatus = {
    MELODY: ContentConnectionStatus;
    CLASSIC: ContentConnectionStatus;
    HARMONY: ContentConnectionStatus;
    TRIDION: ContentConnectionStatus;
};

export type ReleaseNotesBundle = ContentBundle<string, string, string, string>;

export type ContentTypes = keyof ContentList;
export type DisplayMode = "WIZARD" | "TABLE" | "DEFAULT";
export type WizardSteps = "CONTENT_SELECTOR" | "TABLE" | "HELP";
export type LocalContentStatus =
    | "ALL_CONTENT_DOWNLOADED"
    | "SOME_CONTENT_DOWNLOADED"
    | "NO_CONTENT_DOWNLOADED";

export type SupportedContent = {
    types: ContentTypes[];
    current?: ContentTypes;
};

export interface ExpandedKeys {
    [key: string]: boolean;
}

export type Status = "loading" | "succeeded" | "failed" | "idle";
