import { getState } from "../../redux/CMTStore";
import * as Hooks from "../../redux/hooks";
import "./scss/jobs.scss";
import ProgressBar from "react-bootstrap/ProgressBar";
import type { Job } from "./jobsSlice";

interface Props {
    name: string | undefined;
    compact: boolean;
}

// If matching job exists: display a progress bar and name of active task
const JobProgress = (props: Props): JSX.Element => {
    const state = getState(Hooks.useAppSelector((state) => state));

    const matchingJobs: Job[] = state.jobs.allJobs.filter(
        (job) => job.type === "download" && job.data.package === props.name,
    );

    // Use the latest one if there are more than one
    matchingJobs.sort((a: Job, b: Job) => (a.id < b.id ? 1 : b.id < a.id ? -1 : 0));
    const job = matchingJobs[0];

    const activeTask = job === undefined ? undefined : job.activeTask;
    const isFinished = job === undefined ? false : job.isFinished;
    const step = job === undefined ? 0 : job.tasks.length;

    return (
        <div className={props.compact ? "progress-bar-compact" : "progress-bar-full"}>
            {activeTask !== undefined && !isFinished && (
                <div className="container">
                    <ProgressBar
                        className="slider"
                        now={activeTask.progress}
                        label={`${activeTask.progress}%`}
                    />
                    <div className="label">
                        Step {step}: {activeTask.name}
                    </div>
                </div>
            )}
            {activeTask !== undefined && isFinished && (
                <div className="container">
                    <ProgressBar
                        className="slider"
                        now={100}
                        label={`100%`}
                        variant="success"
                    />
                    <div className="label">Complete</div>
                </div>
            )}
        </div>
    );
};

export default JobProgress;
