import { Button } from "primereact/button";
import React from "react";
import type { MyTreeNode } from "../../../../../../model/types/TreeNode";
import styles from "./scss/module-column.module.scss";
import "./scss/column.scss";
import { ONLINE_DOCS_LINK } from "../../../../../common/common";

interface ModuleColumnBodyProps {
    rowData: MyTreeNode;
    isExpanded?: boolean;
}

export const ModuleColumnBody: React.FC<ModuleColumnBodyProps> = (props) => (
    <>
        {props.rowData.data?.moduleVersion && (
            <Button
                className="p-button-rounded p-button-text help-icon"
                icon={`pi pi-info-circle`}
                tooltip="Click To Open Release Notes"
                tooltipOptions={{ position: "bottom" }}
                onClick={(): void => {
                    if (props.rowData.data?.meta?.releaseNotesLink) {
                        window.open(props.rowData.data.meta.releaseNotesLink, "_blank");
                    } else if (props.rowData.data?.help) {
                        window.open(props.rowData.data.help.url, "_blank");
                    } else {
                        window.open(ONLINE_DOCS_LINK, "_blank");
                    }
                }}
            />
        )}
        {props.isExpanded ? (
            <span className={styles.moduleName + " expanded"}>
                {props.rowData.data?.moduleName}
            </span>
        ) : (
            <span className={styles.moduleName}>{props.rowData.data?.moduleName}</span>
        )}
    </>
);
