import type { PayloadAction } from "@reduxjs/toolkit";
import { createSlice } from "@reduxjs/toolkit";
import type {
    DownloadFailure,
    GenericServerMessage,
    Props as ShutdownNoticeProps,
} from "./features/common/ShutdownNotice";
import {
    GENERIC_SERVER_MESSAGE,
    GENERIC_SHUTDOWN_NOTICE,
    DISPLAY_DOWNLOAD_FAILURE_NOTICE,
} from "./features/common/ShutdownNotice";
import {
    displayServerConfirmationProps,
    sendServerConfirmationProps,
} from "./features/common/ServerConfirmation";
import type { DisplayMode } from "./features/types";
import type { RootState } from "./redux/CMTStore";

export type AppState = {
    displayMode: DisplayMode;
    filterData?: string[];
    shutdownNotice?: ShutdownNoticeProps;
    serverConfirmation?: displayServerConfirmationProps;
};

const initialState: AppState = {
    displayMode: "DEFAULT",
    filterData: [],
};

export const slice = createSlice({
    name: "app",
    initialState: initialState,
    reducers: {
        setDisplayMode: (state, action: PayloadAction<DisplayMode>) => {
            state.displayMode = action.payload;
        },
        setFilterData: (state, action: PayloadAction<string[]>) => {
            state.filterData = action.payload;
        },
        displayServerMessage: (state, action: PayloadAction<GenericServerMessage>) => {
            state.shutdownNotice = {
                type: GENERIC_SERVER_MESSAGE,
                severity: action.payload.severity,
                shutdownAfter: action.payload.shutdownAfter,
                title: action.payload.title,
                body: action.payload.body,
            };
        },
        displayShutdownNotice: (state, action: PayloadAction<string>) => {
            state.shutdownNotice = {
                type: GENERIC_SHUTDOWN_NOTICE,
                body: action.payload,
            };
        },
        displayDownloadFailureNotice: (
            state,
            action: PayloadAction<DownloadFailure[]>,
        ) => {
            const shouldShoutdown =
                window.parent.windowCommunication !== undefined ? true : false;
            state.shutdownNotice = {
                type: DISPLAY_DOWNLOAD_FAILURE_NOTICE,
                failedDownloads: action.payload,
                shutdownAfter: shouldShoutdown,
            };
        },
        clearShutdownNotice: (state) => {
            state.shutdownNotice = undefined;
        },
        requestShutdown: (state) => {
            // Do nothing.
        },
        requestContext: (state) => {
            // Do nothing.
        },
        displayServerConfirmationMsg: (
            state,
            action: PayloadAction<displayServerConfirmationProps | undefined>,
        ) => {
            state.serverConfirmation = action.payload;
        },
        requestSetServerConfirmationResponse: (
            state,
            action: PayloadAction<sendServerConfirmationProps>,
        ) => {
            //Send response to backend
        },
    },
    extraReducers: {},
});

export type ReducerActions = typeof slice.actions;
export type ReducerState = ReturnType<typeof slice.reducer>;

export const actions = {
    ...slice.actions,
};

export const getState = (state: RootState): ReducerState => state.app;

export const reducer = slice.reducer;
