import type { Content } from "../BaseContent/Content";
import { ContentManager } from "../BaseContent/ContentModel";
import type { MelodyManifest } from "./MelodyManifest";
import { MelodyContent } from "./MelodyContent";

export class MelodyManager extends ContentManager<MelodyManifest, MelodyContent> {
    protected getNewContent = (
        name: string,
        data: MelodyManifest,
        shouldIncludePreReleaseVersions: boolean,
    ): Content<MelodyManifest> => {
        return new MelodyContent(name, data, shouldIncludePreReleaseVersions);
    };
}
