import * as React from "react";
import type { MyTreeNode } from "../../../../../../model/types/TreeNode";
import JobProgress from "../../../../../Jobs/JobProgress";

export type Props = {
    node: MyTreeNode;
};

export const ProgressColumn: React.FC<Props> = (props): JSX.Element => {
    const packageName = props.node.data?.packageName;
    return (
        <>
            <JobProgress name={packageName} compact={true} />
        </>
    );
};
