import React from "react";
import { Status } from "../../types";

interface Props {
    libraryName: string;
    updateStatus: Status;
    latestVersion: string;
    currentVersion: string;
    isUpToDate: boolean;
    releaseNotesUrl?: string;
}

const UpdateStatus = (props: Props): JSX.Element => {
    const { libraryName, updateStatus, latestVersion, currentVersion, isUpToDate } =
        props;

    return (
        <div className="d-flex flex-column">
            {isUpToDate && <span className="mt-1">{libraryName} is up to date</span>}
            {updateStatus === "succeeded" && !isUpToDate && (
                <span className="mt-1">
                    Restart MCC to complete installation of {libraryName} v{latestVersion}
                </span>
            )}
            {updateStatus === "loading" && (
                <span className="mt-1">
                    Downloading {libraryName} {latestVersion}
                </span>
            )}
            {updateStatus === "failed" && (
                <span className="mt-1">
                    An error occurred while installing the latest {libraryName} update.
                </span>
            )}
            <span className="me-2">
                Running {libraryName} version: v{currentVersion}
            </span>
            {props.releaseNotesUrl !== undefined && (
                <a href={props.releaseNotesUrl} target="_blank" rel="noopener noreferrer">
                    {props.releaseNotesUrl}
                </a>
            )}
        </div>
    );
};

export default UpdateStatus;
