import type { Content } from "../BaseContent/Content";
import { ContentManager } from "../BaseContent/ContentModel";
import type { HarmonyManifest } from "./HarmonyManifest";
import { HarmonyContent } from "./HarmonyContent";

export class HarmonyManager extends ContentManager<HarmonyManifest, HarmonyContent> {
    protected getNewContent = (
        name: string,
        data: HarmonyManifest,
        includePreReleaseVersions: boolean,
    ): Content<HarmonyManifest> => {
        return new HarmonyContent(name, data, includePreReleaseVersions);
    };
}
