/* eslint-disable @typescript-eslint/prefer-regexp-exec */
import type { RowData } from "../types/RowData";
import type { CategoryInjector } from "../types/TreeNode";
import { getKeys } from "../types/Utils";

const defaultCategories: CategoryInjector = (data: RowData): string[] => {
    if (data.meta?.category === "") return [];
    const repoCategory = data.moduleCategory?.split("/") ?? [];
    const name = data.packageName?.replace(/@microchip\/|@mchp-mcc\//g, "") ?? "";
    if (name !== data.moduleName) repoCategory.push(data.moduleName ?? name);
    return repoCategory;
};

const resolveClassicCategory: CategoryInjector = (data: RowData): string[] => {
    const repoCategory = data.meta?.category ? [data.meta?.category] : [];
    repoCategory.push(data.meta?.name ?? data.moduleName ?? "Invalid Module");
    return repoCategory;
};

const deviceArch: CategoryInjector = (data: RowData): string[] => {
    const type = getType(data.meta?.keywords ?? []);

    let repoCategory: string[];
    switch (type) {
        case "Classic":
            repoCategory = resolveClassicCategory(data);
            repoCategory.unshift(type);
            return repoCategory;
        case "Harmony":
            repoCategory = resolveClassicCategory(data);
            repoCategory.unshift(type);
            return repoCategory;
        case "Melody":
            repoCategory = defaultCategories(data);
            break;
        case "Other":
        default:
            repoCategory = [];
    }

    if (data.moduleName && data.moduleCategory !== "Framework") {
        if (
            data.packageName?.match("8-bit|8bit|pic8|avr|pic18") ||
            data.moduleName?.match("8-bit")
        ) {
            repoCategory.unshift("8-bit");
        } else if (
            data.packageName?.match("pic24|16bit|dspic33|irda") ||
            data.moduleName?.match("16-bit")
        ) {
            repoCategory.unshift("16-bit");
        } else {
            repoCategory.unshift("Common");
        }
    }
    repoCategory.unshift(type);

    return repoCategory;
};

const CategoryInjectors = {
    "Device Architecture": deviceArch,
    Default: defaultCategories,
};

export const getCategoryInjectorOptions = getKeys(CategoryInjectors);
export type CategoryInjectorOptions = keyof typeof CategoryInjectors;
export const getCategory = (
    categoryInjector?: CategoryInjectorOptions,
): CategoryInjector => {
    return (data: RowData): string[] => {
        return CategoryInjectors[categoryInjector ?? "Default"]?.(data);
    };
};
type ContentTypes = "Melody" | "Classic" | "Harmony" | "Other";

function getType(keywords: string[]): ContentTypes {
    if (keywords.includes("classic")) {
        return "Classic";
    } else if (keywords.includes("melody")) {
        return "Melody";
    } else if (keywords.includes("harmony")) {
        return "Harmony";
    }

    return "Other";
}
