import { Button } from "primereact/button";
import * as React from "react";
import type { ContentTypes, ReleaseNotesBundle } from "../../types";
import { createContentCard } from "./ContentCard";
import { LibrariesAccordian } from "./LibrariesAccordian";
import type { ContentBundle } from "../../types";
import type { Meta } from "../../../model/BaseContent/Manifest";
import { SEE_MORE_DETAILS_LINK } from "../../common/common";
import {
    MELODY_TECHNICAL_HELP_LINK,
    CLASSIC_TECHNICAL_HELP_LINK,
    HARMONY_TECHNICAL_HELP_LINK,
} from "../../common/common";
import { deviceSupports } from "./DeviceSupport";
import * as Hooks from "../../../redux/hooks";
import { getState, actions } from "../wizardSlice";

export type Props = {
    supportedContentTypes: ContentTypes[];
    setContentType?: (value: ContentTypes) => void;
    catalogData: ContentBundle<Meta[], Meta[], Meta[], Meta[]>;
};
export interface Explanation {
    title: string;
    details?: JSX.Element;
}

const defaultReleaseNotes: ReleaseNotesBundle = {
    MELODY: "https://www.microchip.com",
    CLASSIC: "https://www.microchip.com/mcc",
    HARMONY: "https://www.microchip.com/harmony",
    TRIDION: "",
};

const explanations: Record<ContentTypes, string[]> = {
    MELODY: [
        "Supports the MCC Builder",
        "Supports content versioning at driver level",
        "An iteration of MCC Generated Code",
        "Works both on- and off-line",
    ],
    CLASSIC: [
        "Development process you are accustomed to",
        "All components and libraries that you have used before",
    ],
    HARMONY: [
        "Embedded Software Development Framework for 32-bit Microcontrollers and Microprocessors",
    ],
    TRIDION: [""],
};

export const ContentSelector: React.FC<Props> = (props): JSX.Element => {
    const dispatch = Hooks.useAppDispatch();
    const state = getState(Hooks.useAppSelector((state) => state));

    const releaseNotes = state.releaseNotes;

    React.useEffect(() => {
        if (!state.releaseNotes) {
            dispatch(actions.requestReleaseNotes());
        }
    }, [state.releaseNotes]);

    const createContentButton = (contentType: ContentTypes): JSX.Element => {
        const prefix = contentType === "HARMONY" ? "Select MPLAB " : "Select MCC ";
        return (
            <Button
                className="p-button-rounded p-button-primary p-button-lg p-mr-2 content-btn"
                label={
                    prefix +
                    contentType.charAt(0).toUpperCase() +
                    contentType.toLowerCase().slice(1)
                }
                onClick={(): void => {
                    props.setContentType?.(contentType);
                }}
                disabled={
                    props.supportedContentTypes &&
                    !props.supportedContentTypes.includes(contentType)
                }
            />
        );
    };
    const createMoreDetailsHeader = (): JSX.Element => {
        return props.supportedContentTypes.length > 0 ? (
            <h3>Still unsure which content type is right for your project?</h3>
        ) : (
            <h3>Device not supported!</h3>
        );
    };
    const createMoreDetails = (): JSX.Element => {
        return (
            <div className={"details"}>
                <div className={"row justify-content-center"}>
                    {createMoreDetailsHeader()}
                </div>
                <div className={"row justify-content-center"}>
                    <Button
                        className="p-button-rounded p-button-primary p-button-lg p-mr-2 auto-width"
                        label={"See More Details"}
                        onClick={(): void => {
                            window.open(SEE_MORE_DETAILS_LINK, "_blank");
                        }}
                        tooltip="Click for a video"
                    />
                </div>
            </div>
        );
    };

    const createReleaseNotesButton = (releaseNotesUrl: string): JSX.Element => {
        return (
            <span
                className={"text-link release-notes"}
                onClick={(e): void => {
                    e.stopPropagation();
                    window.open(releaseNotesUrl, "_blank");
                }}
            >
                Release notes and supported devices
            </span>
        );
    };

    const createContentCards = (): JSX.Element => {
        return (
            <div className={"row"}>
                <div className={"col-md-4"}>
                    {createContentCard(
                        "MELODY",
                        explanations.MELODY,
                        deviceSupports.MELODY,
                        createContentButton,
                        !props.supportedContentTypes.includes("MELODY"),
                        MELODY_TECHNICAL_HELP_LINK,
                    )}
                    {createReleaseNotesButton(
                        releaseNotes?.MELODY ?? defaultReleaseNotes.MELODY,
                    )}
                </div>
                <div className={"col-md-4"}>
                    {createContentCard(
                        "CLASSIC",
                        explanations.CLASSIC,
                        deviceSupports.CLASSIC,
                        createContentButton,
                        !props.supportedContentTypes.includes("CLASSIC"),
                        CLASSIC_TECHNICAL_HELP_LINK,
                    )}
                    {createReleaseNotesButton(
                        releaseNotes?.CLASSIC ?? defaultReleaseNotes.CLASSIC,
                    )}
                </div>
                <div className={"col-md-4"}>
                    {createContentCard(
                        "HARMONY",
                        explanations.HARMONY,
                        deviceSupports.HARMONY,
                        createContentButton,
                        !props.supportedContentTypes.includes("HARMONY"),
                        HARMONY_TECHNICAL_HELP_LINK,
                    )}
                    {createReleaseNotesButton(
                        releaseNotes?.HARMONY ?? defaultReleaseNotes.HARMONY,
                    )}
                </div>
            </div>
        );
    };
    const createSelectHeading = (): JSX.Element => {
        return <h2 className={"row justify-content-center"}>Select a Content Type</h2>;
    };

    const createLibraryTable = (): JSX.Element => {
        return (
            <div className={"details"}>
                <div className={"row justify-content-center"}>
                    <h3>
                        Library support may be a key factor in your choice of MCC flavor:
                    </h3>
                </div>
                <div className={"row justify-content-center"}>
                    <LibrariesAccordian catalogData={props.catalogData} />
                </div>
            </div>
        );
    };

    return (
        <div className={"container"}>
            {createSelectHeading()}
            {createContentCards()}
            {createLibraryTable()}
            {createMoreDetails()}
        </div>
    );
};
