import { values } from "../../util/objects";
import type { ContentId } from "../types/ContentId";
import type { RowData, VersionStatus } from "../types/RowData";
import type { MyTreeNode } from "../types/TreeNode";
import { addTreeNode, populateStatus } from "../types/TreeNode";
import { getKeys } from "../types/Utils";
import type { CategoryInjectorOptions } from "./CategorySort";
import { getCategory } from "./CategorySort";
import type { Content } from "./Content";
import type { ContentManifests, Manifest } from "./Manifest";

export type Props = {
    setActiveContent: (contentIds: ContentId[]) => void;
    downloadContent: (contentIds: ContentId[]) => void;
};

export type ContentModel<
    M extends Manifest = Manifest,
    T extends Content<M> = Content<M>,
> = {
    model: Record<string, T>;
    initializeContent: (
        content: ContentManifests<M>,
        selectedVersions: ContentId[],
        availableContent: ContentId[],
        shouldIncludePreReleaseVersions: boolean,
    ) => void;
    getProcessedData: () => MyTreeNode[];
    getAvailableData: (availableIds: ContentId[]) => MyTreeNode[];
    getOptionalRemoteData: (availableIds: ContentId[]) => MyTreeNode[];
    getRequiredRemoteData: (availableIds: ContentId[]) => MyTreeNode[];
    isUpdateAvailable: () => boolean;
    setActiveContent: (contentIds: ContentId[]) => ContentId[];
    selectLatestContent: (contentIds: ContentId[]) => ContentId[];
    getLatestVersions: (contentIds?: ContentId[]) => ContentId[];
    getSelectedVersions: (contentIds?: ContentId[]) => ContentId[];
    getDefaultModules: (contentIds?: ContentId[]) => ContentId[];
    getVersionStatus: (id: string, version: string) => VersionStatus | undefined;
    resolveIds: (contentIds: ContentId[]) => ContentId[];
    getModels: (contentIds?: ContentId[]) => T[];
    getContentWithUpdates: (contentIds?: ContentId[]) => ContentId[];
};

export abstract class ContentManager<
    M extends Manifest = Manifest,
    T extends Content<M> = Content<M>,
> implements ContentModel<M, T>
{
    public model: Record<string, T> = {};
    public shouldIncludePreReleaseVersions = false;
    private categoryInjector: CategoryInjectorOptions;
    private updatesAvailable = false;

    constructor(categoryInjector: CategoryInjectorOptions) {
        this.categoryInjector = categoryInjector;
    }

    public initializeContent = (
        content: ContentManifests<M>,
        selectedVersions: ContentId[],
        availableContent: ContentId[],
        shouldIncludePreReleaseVersions: boolean,
    ): void => {
        this.model = {};
        this.shouldIncludePreReleaseVersions = shouldIncludePreReleaseVersions;
        this.addContent(content);
        values(this.model).forEach((mod) => {
            const latestLocalVersion = mod.getLatestLocalVersion();
            if (latestLocalVersion) {
                mod.setSelectedVersion(mod.getLatestLocalVersion());
            }
        });
        //assign available content
        availableContent.forEach((contentId) => {
            if (this.model[contentId.name] && contentId.version !== "*") {
                this.model[contentId.name].setSelectedVersion(
                    contentId.version ?? this.model[contentId.name].getSelectedVersion(),
                );
            }
        });
        selectedVersions.forEach((contentId) => {
            if (this.model[contentId.name]) {
                this.model[contentId.name].setSelectedVersion(
                    contentId.version ?? this.model[contentId.name].getSelectedVersion(),
                );
            }
        });
    };

    public getProcessedData = (): MyTreeNode[] => {
        const nodes: MyTreeNode[] = [];
        values(this.model).forEach((module) => {
            addTreeNode(
                nodes,
                module.getSelectedModule(),
                getCategory(this.categoryInjector),
            );
        });
        //Update status columns for parent nodes
        this.updatesAvailable = populateStatus(nodes) === "UPDATE";
        return nodes;
    };

    public getOptionalRemoteData = (availableIds: ContentId[]): MyTreeNode[] => {
        const nodes: MyTreeNode[] = [];
        values(this.model).forEach((module) => {
            const selectedData = module.getSelectedModule();

            if (!selectedData.meta?.defaultSelection) {
                this.addRemoteContentTreeNode(selectedData, availableIds, nodes);
            }
        });

        //Update status columns for parent nodes
        this.updatesAvailable = populateStatus(nodes) !== "LATEST";
        return nodes;
    };

    public getRequiredRemoteData = (availableIds: ContentId[]): MyTreeNode[] => {
        const nodes: MyTreeNode[] = [];
        values(this.model).forEach((module) => {
            const selectedData = module.getSelectedModule();

            this.addRemoteContentTreeNode(selectedData, availableIds, nodes);
        });

        return nodes;
    };

    private addRemoteContentTreeNode = (
        selectedData: RowData,
        availableIds: ContentId[],
        nodes: MyTreeNode[],
    ): void => {
        const isAvailable = availableIds.find((id) => id.name === selectedData.id);

        if (
            isAvailable &&
            selectedData.versions?.[selectedData.moduleVersion ?? ""] !== "LOCAL"
        ) {
            addTreeNode(nodes, selectedData, getCategory(this.categoryInjector));
        }
    };

    public getAvailableData = (availableIds: ContentId[]): MyTreeNode[] => {
        const nodes: MyTreeNode[] = [];
        values(this.model).forEach((module) => {
            if (
                this.isAvailable(
                    {
                        name: module.id,
                        version: module.getSelectedVersion(),
                    },
                    availableIds,
                )
            ) {
                addTreeNode(
                    nodes,
                    module.getSelectedModule(),
                    getCategory(this.categoryInjector),
                );
            }
        });

        //Update status columns for parent nodes
        this.updatesAvailable = populateStatus(nodes) !== "LATEST";
        return nodes;
    };

    public getModels = (contentIds?: ContentId[]): T[] => {
        if (!contentIds) return values(this.model);
        return this.resolveIds(contentIds).map((value) => this.model[value.name]);
    };

    public isUpdateAvailable = (): boolean => {
        return this.updatesAvailable;
    };

    private isAvailable = (id: ContentId, availableIds?: ContentId[]): boolean => {
        if (!availableIds) return false;
        for (let i = 0; i < availableIds.length; i++) {
            const availableId = availableIds[i];
            if (availableId.name === id.name) {
                return true;
            }
        }
        return false;
    };

    private addContent = (dataList?: M[]): void => {
        dataList?.forEach((data) => {
            const content = this.getContent?.(data);
            this.model[content.id] = content;
            this.model[content.id]?.setLatestVersion();
        });
    };

    public setActiveContent = (contentIds: ContentId[]): ContentId[] => {
        contentIds.forEach((contentId) => {
            if (this.model[contentId.name]) {
                //ignore content that isn't in the cmt
                this.model[contentId.name].setSelectedVersion(
                    contentId.version ?? this.model[contentId.name].getSelectedVersion(),
                );
            }
        });
        const activeContent: ContentId[] = this.resolveIds(contentIds).map(
            (contentId) => {
                return {
                    ...contentId,
                    version: this.model[contentId.name]?.getSelectedVersion(),
                };
            },
        );
        return activeContent;
    };

    public selectLatestContent = (contentIds: ContentId[]): ContentId[] => {
        const latestContentId: ContentId[] = this.resolveIds(contentIds).map(
            (contentId) => {
                return {
                    ...contentId,
                    version: this.model[contentId.name]?.getLatestVersion(),
                };
            },
        );
        return this.setActiveContent(latestContentId);
    };

    public getLatestVersions = (contentIds?: ContentId[]): ContentId[] => {
        const ids: ContentId[] =
            contentIds ??
            getKeys(this.model).map((name) => {
                return {
                    name: name,
                };
            });
        const latestContentId: ContentId[] = this.resolveIds(ids).map((contentId) => {
            return {
                ...contentId,
                version: this.model[contentId.name]?.getLatestVersion(),
            };
        });
        return latestContentId;
    };

    public getDefaultModules = (contentIds?: ContentId[]): ContentId[] => {
        const ids: ContentId[] = this.resolveIds(contentIds ?? []);
        return ids
            .filter((id) => {
                return this.model[id.name].getMeta()?.defaultSelection !== undefined;
            })
            .map((contentId) => {
                return {
                    name: contentId.name,
                    version: this.model[contentId.name].getSelectedVersion(),
                };
            });
    };

    public getSelectedVersions = (contentIds?: ContentId[]): ContentId[] => {
        const ids: ContentId[] = this.resolveIds(contentIds ?? []);
        const newIds = ids.map((contentId) => {
            return {
                name: contentId.name,
                version: this.model[contentId.name].getSelectedVersion(),
            };
        });

        return newIds;
    };

    public getVersionStatus = (
        id: string,
        version: string,
    ): VersionStatus | undefined => {
        return this.model[id]?.getVersionStatus(version);
    };

    public resolveIds = (contentIds: ContentId[]): ContentId[] => {
        const resolvedIds: ContentId[] = [];
        contentIds.forEach((contentId) => {
            if (this.model[contentId.name]) {
                resolvedIds.push({
                    name: contentId.name,
                    version:
                        contentId.version ??
                        this.model[contentId.name]?.getSelectedVersion(),
                });
            }
        });
        return resolvedIds;
    };

    public getContentWithUpdates = (contentId?: ContentId[]): ContentId[] => {
        if (!this.isUpdateAvailable()) return [];

        return this.getModels(contentId)
            .filter((content) => content.getSelectedStatus() !== "LATEST")
            .map((content) => ({
                name: content.id,
                version: content.getSelectedVersion(),
            }));
    };

    private getContent = (data: M): T => {
        const content = this.model[data.name];
        if (content) {
            content.addContent(data);
            return content;
        }
        return this.getNewContent(data.name, data, this.shouldIncludePreReleaseVersions);
    };

    protected abstract getNewContent: (
        name: string,
        data: M,
        shouldIncludePreReleaseVersions: boolean,
    ) => T;
}
