import type { Meta } from "@mchp-mcc/scf-catalog/src/ContentEntry";
import type { PayloadAction } from "@reduxjs/toolkit";
import { createSlice } from "@reduxjs/toolkit";
import type { CategoryInjectorOptions } from "../../model/BaseContent/CategorySort";
import type { Manifest } from "../../model/BaseContent/Manifest";
import type { ContentId } from "../../model/types/ContentId";
import { getKeys, values } from "../../model/types/Utils";
import type { RootState } from "../../redux/CMTStore";
import type {
    ContentBundle,
    ContentIdBundle,
    ContentList,
    ContentListConnectionStatus,
    ContentTypes,
    Status,
} from "../types";

type SelectionState = {
    checked: boolean;
    partialChecked: boolean;
};

export interface UsedContent {
    type: string;
    name: string;
    version: string;
}

export type SelectedNode = Record<string, SelectionState>;
export type ViewMode = "all" | "available";

export type ContentState = {
    data: ContentList;
    usedContent: UsedContent[];
    registries: string[];
    defaultRegistries?: string[];
    gitRepo: string;
    defaultGitRepo: string;
    isChangingGitRepo: boolean;
    availableVersions: ContentIdBundle;
    catalogData: ContentBundle<Meta[], Meta[], Meta[], Meta[]>;
    contentConnectionErrors: ContentListConnectionStatus;
    searchTerms: string[];
    selectedVersions: ContentId[];
    selectedVersionsDiff: ContentId[];
    categorySort: CategoryInjectorOptions;
    isFetching: Status;
    isDownloading: Status;
    viewMode: ViewMode;
    isApplying: boolean;
    isCalculatingCompatability: boolean;
    contentManagerUpdateStatus: Status;
    currentVersion: string;
    shouldIncludePreReleaseVersions: boolean;
    latestContentManagerVersion?: string;
    frontendLatestVersion?: string;
    installedFrontendVersion?: string;
    frontendUpdateStatus: Status;
    showMCCFrontendUpdate: boolean;
};

const currentVersion = process.env.REACT_APP_VERSION ?? "unknown version";

export const initialContentBundle = {
    MELODY: [],
    CLASSIC: [],
    HARMONY: [],
    TRIDION: [],
};

const initialContentConnectionErrors: ContentListConnectionStatus = {
    MELODY: {
        hasConnection: false,
        local: "",
        remote: "",
    },
    CLASSIC: {
        hasConnection: false,
        local: "",
        remote: "",
    },
    HARMONY: {
        hasConnection: false,
        local: "",
        remote: "",
    },
    TRIDION: {
        hasConnection: false,
        local: "",
        remote: "",
    },
};

export const initialState: ContentState = {
    data: {
        MELODY: [],
        CLASSIC: [],
        HARMONY: [],
        TRIDION: [],
    },
    usedContent: [],
    registries: [],
    gitRepo: "",
    defaultGitRepo: "",
    availableVersions: initialContentBundle,
    catalogData: initialContentBundle,
    contentConnectionErrors: initialContentConnectionErrors,
    searchTerms: [],
    selectedVersions: [],
    selectedVersionsDiff: [],
    categorySort: "Default",
    isFetching: "idle",
    isDownloading: "idle",
    isChangingGitRepo: false,
    viewMode: "available",
    isApplying: false,
    isCalculatingCompatability: false,
    contentManagerUpdateStatus: "idle",
    currentVersion: currentVersion,
    shouldIncludePreReleaseVersions: false,
    frontendUpdateStatus: "idle",
    showMCCFrontendUpdate: false,
};

const processSelectedVersions = (
    selectedVersions: ContentId[],
    data: Manifest[],
): ContentId[] => {
    let processedVersions = selectedVersions;
    if (selectedVersions.length > 0) {
        processedVersions = selectedVersions.filter((id) => {
            const entry = data.find((m) => m.name === id.name);
            return entry?.version !== id.version;
        });
    }
    return processedVersions;
};

export const contentSlice = createSlice({
    name: "content",
    initialState: initialState,
    reducers: {
        setSearchTerms: (state, action: PayloadAction<string[]>) => {
            state.searchTerms = action.payload;
        },
        setCategorySort: (state, action: PayloadAction<CategoryInjectorOptions>) => {
            state.categorySort = action.payload;
        },
        setSelectedVersions: (state, action: PayloadAction<ContentId[]>) => {
            state.selectedVersions = action.payload;
            state.selectedVersionsDiff = processSelectedVersions(
                action.payload,
                values(state.data).flat(),
            );
        },
        setSelectedVersion: (state, action: PayloadAction<ContentId>) => {
            const id = state.selectedVersions.findIndex(
                (contentA) => contentA.name === action.payload.name,
            );
            if (state.selectedVersions[id]) {
                state.selectedVersions[id].version = action.payload.version;
            } else {
                state.selectedVersions.push(action.payload);
            }
            if (state.selectedVersionsDiff[id]) {
                state.selectedVersionsDiff[id].version = action.payload.version;
            } else {
                state.selectedVersionsDiff.push(action.payload);
            }
        },
        requestInitialContent: (
            state,
            action: PayloadAction<ContentTypes | undefined>,
        ) => {
            //Reset Data when asking for new content
            state.data = initialContentBundle;
            state.availableVersions = initialContentBundle;

            state.isFetching = "loading";
        },
        requestDownload: (state, action: PayloadAction<ContentIdBundle>) => {
            state.isDownloading = "loading";
        },
        requestContentManagerDownload: (state, action: PayloadAction<string>) => {
            state.contentManagerUpdateStatus = "loading";
        },
        requestSetAvailableContent: (state, action: PayloadAction<ContentIdBundle>) => {
            state.isFetching = "loading";
        },
        requestCompatibilityErrors: (state, action: PayloadAction<ContentIdBundle>) => {
            state.isCalculatingCompatability = true;
        },
        requestContentConnectionsStatus: (
            state,
            action: PayloadAction<ContentIdBundle>,
        ) => {
            // Do nothing.
        },
        requestRefresh: (state) => {
            state.isFetching = "loading";
        },
        requestRefreshFor: (state, action: PayloadAction<ContentId[]>) => {
            console.log("Request refresh for " + JSON.stringify(action.payload));
        },
        requestShouldIncludePreReleaseVersions: (
            state,
            action: PayloadAction<undefined>,
        ) => {
            // Do nothing.
        },
        setContent: (state, action: PayloadAction<ContentList>) => {
            state.data.MELODY = action.payload.MELODY ?? state.data.MELODY;
            state.data.CLASSIC = action.payload.CLASSIC ?? state.data.CLASSIC;
            state.data.HARMONY = action.payload.HARMONY ?? state.data.HARMONY;
            state.data.TRIDION = action.payload.TRIDION ?? state.data.TRIDION;

            state.selectedVersionsDiff = processSelectedVersions(
                state.selectedVersionsDiff,
                values(state.data).flat(),
            );

            if (state.isDownloading !== "loading") state.isApplying = false;
            state.isDownloading = "succeeded";
            state.isFetching = "succeeded";
        },
        setSingleContent: (state, action: PayloadAction<Partial<ContentList>>) => {
            getKeys(action.payload).forEach((contentType) => {
                const content = action.payload[contentType as ContentTypes];
                if (content) {
                    state.data[contentType as ContentTypes] = content;

                    state.selectedVersionsDiff = processSelectedVersions(
                        state.selectedVersionsDiff,
                        content,
                    );
                }
            });

            if (state.isDownloading !== "loading") state.isApplying = false;
            if (state.isDownloading === "loading") state.isDownloading = "succeeded";
            if (state.isFetching === "loading") state.isFetching = "succeeded";
        },
        setAvailableVersions: (state, action: PayloadAction<ContentIdBundle>) => {
            state.availableVersions = {
                ...initialContentBundle,
                ...action.payload,
            };
        },
        clearDownloadProgress: (state) => {
            state.isApplying = false;
            state.isDownloading = "idle";
            state.isFetching = "idle";
        },
        setCompatibilityErrors: (
            state,
            action: PayloadAction<Record<string, Record<string, string[]>>>,
        ) => {
            const errors = action.payload ?? {};
            const setErrors = <T extends Manifest>(manifest: T): void => {
                manifest.versionCompatibilityErrorMap = errors[manifest.name] ?? {};
            };
            const setAllErrors = <T extends Manifest>(manifests: T[]): void =>
                manifests.forEach((manifest) => setErrors(manifest));

            setAllErrors(state.data.CLASSIC);
            setAllErrors(state.data.MELODY);
            setAllErrors(state.data.HARMONY);
            setAllErrors(state.data.TRIDION);

            state.isCalculatingCompatability = false;
        },
        setContentConnectionsStatus: (
            state,
            action: PayloadAction<ContentListConnectionStatus>,
        ) => {
            state.contentConnectionErrors = action.payload;
        },
        setIncludePreReleaseVersions: (state, action: PayloadAction<boolean>) => {
            state.shouldIncludePreReleaseVersions = action.payload;
        },
        setUsedContent: (state, action: PayloadAction<UsedContent[]>) => {
            state.usedContent = action.payload;
        },
        setViewMode: (state, action: PayloadAction<ViewMode>) => {
            state.viewMode = action.payload;
        },
        requestCatalogData: (state) => {
            // Do nothing
        },
        requestSetRegistries: (state, action: PayloadAction<string[]>) => {
            state.registries = action.payload;
        },
        setRegistries: (state, action: PayloadAction<string[]>) => {
            state.registries = action.payload;
        },
        setRegistryDefaults: (state, action: PayloadAction<string[]>) => {
            state.defaultRegistries = action.payload;
        },
        requestContentManagerUpdate: (state) => {
            // Do nothing
        },
        setContentManagerUpdateStatus: (state, action: PayloadAction<Status>) => {
            state.contentManagerUpdateStatus = action.payload;
        },
        setCatalogData: (
            state,
            action: PayloadAction<Partial<ContentBundle<Meta[], Meta[], Meta[], Meta[]>>>,
        ) => {
            getKeys(action.payload).forEach((contentType) => {
                const content = action.payload[contentType as ContentTypes];

                if (content) {
                    state.catalogData[contentType as ContentTypes] = content;
                }
            });
        },
        setApply: (state, action: PayloadAction<boolean>) => {
            state.isApplying = action.payload;
        },
        setLatestContentManagerVersion: (state, action: PayloadAction<string>) => {
            state.latestContentManagerVersion = action.payload;
            state.contentManagerUpdateStatus = "idle";
        },
        setCurrentContentManagerVersion: (state, action: PayloadAction<string>) => {
            state.currentVersion = action.payload;
        },
        setLatestMCCFrontendVersion: (state, action: PayloadAction<string>): void => {
            state.frontendLatestVersion = action.payload;
        },
        setInstalledMCCFrontendVersion: (state, action: PayloadAction<string>): void => {
            state.installedFrontendVersion = action.payload;
        },
        setMCCFrontendUpdateStatus: (state, action: PayloadAction<Status>): void => {
            state.frontendUpdateStatus = action.payload;
        },
        requestMCCFrontendDownload: (state, action: PayloadAction<string>): void => {
            state.frontendUpdateStatus = "loading";
        },
        requestMCCFrontendUpdate: (state): void => {
            //Do nothing, setInstalledMCCFrontendVersion and setLatestMCCFrontendVersion action will be triggered from backend
        },
        showMCCFrontendUpdate: (state, action: PayloadAction<boolean>): void => {
            state.showMCCFrontendUpdate = action.payload;
        },
    },
    extraReducers: {},
});
export type ReducerActions = typeof contentSlice.actions;
export type ReducerState = ReturnType<typeof contentSlice.reducer>;

export const getState = (state: RootState): ReducerState => state.content;

export const actions = contentSlice.actions;

export const reducer = contentSlice.reducer;
