import { Content } from "../BaseContent/Content";
import type { RowData } from "../types/RowData";
import type { TridionManifest } from "./TridionManifest";

export class TridionContent extends Content<TridionManifest> {
    public getRowData = (manifest: TridionManifest): RowData => {
        const name = manifest.meta?.name ?? manifest.name;
        return {
            id: this.id,
            moduleName: name,
            moduleVersion: manifest.version,
            moduleDescription: manifest.meta?.description ?? "",
            moduleCategory: manifest.meta?.category,
            packageName: manifest.name,
            help: undefined,
            versions: manifest.versionStatusMap,
            status: this.getSelectedStatus(),
            meta: manifest.meta,
            compatibilityErrors: manifest.versionCompatibilityErrorMap,
        };
    };
}
