import type { ExpandedKeys } from "../../../features/types";
import type { RowData } from "../../types/RowData";
import type { MyTreeNode } from "../../types/TreeNode";
import { filterableColumnFields } from "./utility";

export const getExpandedKeysForFilteredNodes = (
    treeTableNodes: MyTreeNode[],
): ExpandedKeys => {
    let expandedKeys: ExpandedKeys = {};
    treeTableNodes.forEach((nodes) => {
        if (nodes.key && isNonLeafNode(nodes)) {
            expandedKeys[nodes.key] = true;
            expandedKeys = {
                ...expandedKeys,
                ...getExpandedKeysForFilteredNodes(nodes.children),
            };
        }
    });
    return expandedKeys;
};

export const expandAllKeys = (treeTableNodes: MyTreeNode[]): ExpandedKeys => {
    let expandedKeys: ExpandedKeys = {};
    treeTableNodes.forEach((nodes) => {
        if (nodes.key && isNonLeafNode(nodes)) {
            expandedKeys[nodes.key] = true;
            expandedKeys = {
                ...expandedKeys,
                ...getExpandedKeysForFilteredNodes(nodes.children),
            };
        }
    });
    return expandedKeys;
};

export const isNonLeafNode = (nodeData: MyTreeNode): number => nodeData.children.length;

export type filterableColumnFieldType =
    | "moduleName"
    | "moduleDescription"
    | "moduleVersion"
    | "moduleCategory"
    | "status";

export const getFilteredTreeTableData = (
    treeTableModels: MyTreeNode[],
    searchTerms: string[],
): MyTreeNode[] => {
    let filteredTreeTableModels: MyTreeNode[] = [];
    const treeNodesToBeFiltered = JSON.parse(
        JSON.stringify(treeTableModels),
    ) as unknown as MyTreeNode[];
    filteredTreeTableModels = treeNodesToBeFiltered.filter((treeNodeToBeFiltered) => {
        if (isNonLeafNode(treeNodeToBeFiltered)) {
            treeNodeToBeFiltered.children = getFilteredTreeTableData(
                treeNodeToBeFiltered.children,
                searchTerms,
            );
            return treeNodeToBeFiltered.children.length;
        }
        return isTreeNodeFiltered(treeNodeToBeFiltered, searchTerms);
    });
    return filteredTreeTableModels;
};

const isTreeNodeFiltered = (
    treeNode: MyTreeNode,
    searchTerms: string[],
): boolean | undefined => {
    return (
        treeNode.data &&
        treeNode.data.id !== "content-manager-ui" &&
        isSearchTermsPresentInNodeData(treeNode.data, searchTerms)
    );
};

const isSearchTermsPresentInNodeData = (
    nodeData: RowData,
    searchTerms: string[],
): boolean => {
    return searchTerms.every((searchTerm) => {
        return (
            (nodeData.meta?.keywords &&
                isSearchTermFoundInNodesKeywords(nodeData.meta.keywords, searchTerm)) ||
            isSearchTermFoundInFilterableFields(nodeData, searchTerm)
        );
    });
};

const isSearchTermFoundInNodesKeywords = (
    nodeKeywords: string[],
    searchTerm: string,
): boolean => {
    return nodeKeywords.some((keyword) =>
        keyword.toLowerCase().includes(searchTerm.toLowerCase()),
    );
};

const isSearchTermFoundInFilterableFields = (
    nodeData: RowData,
    searchTerm: string,
): boolean => {
    return filterableColumnFields.some((field) =>
        nodeData[field]?.toLowerCase().includes(searchTerm.toLowerCase()),
    );
};
