import { createSlice } from "@reduxjs/toolkit";
import type { PayloadAction } from "@reduxjs/toolkit";
import type { RootState } from "../../redux/CMTStore";

export type LoadingScreenState = {
    currentMessage: string;
    currentStep: number;
    totalSteps: number;
    isVisible: boolean;
};

const initialState: LoadingScreenState = {
    currentMessage: "",
    totalSteps: 0,
    currentStep: 0,
    isVisible: false,
};

export const slice = createSlice({
    name: "loadingScreen",
    initialState: initialState,
    reducers: {
        reportLoadingScreenStarted: (state, action: PayloadAction<number>) => {
            state.currentStep = 0;
            state.totalSteps = action.payload;
            state.isVisible = true;
        },
        setLoadingScreenMessage: (state, action: PayloadAction<string>) => {
            state.currentMessage = action.payload;
            state.currentStep = state.currentStep + 1;
        },
        closeLoadingScreen: (state) => {
            state.isVisible = false;
        },
    },
    extraReducers: {},
});

export type ReducerActions = typeof slice.actions;
export type ReducerState = ReturnType<typeof slice.reducer>;

export const actions = {
    ...slice.actions,
};

export const getState = (state: RootState): ReducerState => state.loadingScreen;
export const reducer = slice.reducer;
