import type { Action, Dispatch, Middleware, MiddlewareAPI } from "redux";

export const createRequestMiddleware =
    (request: (action: Action) => void, refresh: () => void): Middleware =>
    (store: MiddlewareAPI) =>
    (next: Dispatch) =>
    (action: Action<string>): void => {
        if (action.type.includes("request")) {
            console.log("Request: " + action.type);
            request(action);
        }
        next(action);
    };
