import type { MenuItem, MenuItemCommandParams } from "primereact/menuitem/menuitem";
import type { MyTreeNode } from "../../../../../../model/types/TreeNode";
import { CODE_REPOSITORY_LINK } from "../../../../../common/common";

export interface OptionProps {
    selectedData?: MyTreeNode;
    currentSelection?: string;
    onSelectLatest?: () => void;
    onExpandAll?: () => void;
    onCollapseAll?: () => void;
    onRefresh?: () => void;
}

export type OptionItem = {
    label: string;
    icon: string;
    command: (e: React.ChangeEvent<HTMLInputElement>) => void;
};

export const getOptionItems = (props: OptionProps): MenuItem[] => {
    return [
        {
            label: "Version History",
            icon: "pi pi-external-link",
            command: (e: MenuItemCommandParams): void => {
                window.open(CODE_REPOSITORY_LINK, "_blank");
            },
        },
        {
            label: "Select Latest Version(s)",
            icon: "pi pi-check-square",
            command: (e: MenuItemCommandParams): void => {
                if (props.currentSelection) {
                    props.onSelectLatest?.();
                }
            },
        },
        {
            label: "Expand All",
            icon: "pi pi-chevron-down",
            command: (e: MenuItemCommandParams): void => {
                props.onExpandAll?.();
            },
        },
        {
            label: "Collapse All",
            icon: "pi pi-chevron-right",
            command: (e: MenuItemCommandParams): void => {
                props.onCollapseAll?.();
            },
        },
        {
            label: "Refresh",
            icon: "pi pi-refresh",
            command: (e: MenuItemCommandParams): void => {
                props.onRefresh?.();
            },
        },
    ];
};
