import type { Meta } from "@mchp-mcc/scf-catalog/src/ContentEntry";
import type { RowData, VersionStatus } from "../types/RowData";
import type { ModuleStatus } from "../types/RowData";
import { getKeys } from "../types/Utils";
import { sort } from "../utils/semver";
import type { Manifest } from "./Manifest";

export abstract class Content<T extends Manifest = Manifest> {
    public id: string;
    protected manifest: T;
    public shouldIncludePreReleaseVersions: boolean;

    constructor(id: string, initialContent: T, shouldIncludePreReleaseVersions: boolean) {
        this.id = id;
        this.manifest = { ...initialContent };
        this.shouldIncludePreReleaseVersions = shouldIncludePreReleaseVersions;
    }

    public addContent = (manifest: T): void => {
        this.manifest = manifest;
    };

    public getSelectedModule = (): RowData => {
        return this.getRowData(this.manifest);
    };

    public getSelectedVersion = (): string => {
        return this.manifest.version;
    };

    public setSelectedVersion = (version: string): void => {
        if (getKeys(this.manifest.versionStatusMap).includes(version)) {
            this.manifest.version = version;
        } else {
            throw new Error(`Version ${version} of ${this.id} is not available`);
        }
    };

    public getVersionStatus = (version: string): VersionStatus => {
        return this.manifest.versionStatusMap[version];
    };

    public setLatestVersion = (): void => {
        this.setSelectedVersion(this.getLatestVersion());
    };

    public getSelectedStatus = (): ModuleStatus => {
        if (this.manifest.version === this.getLatestVersion()) {
            //Active == Latest
            return this.manifest.versionStatusMap[this.manifest.version] === "LOCAL"
                ? "LATEST"
                : "AVAILABLE";
        } else {
            //Active != Latest
            return this.manifest.versionStatusMap[this.manifest.version] === "LOCAL"
                ? "UPDATE"
                : "AVAILABLE";
        }
    };

    public getMeta = (): Meta | undefined => {
        return this.manifest.meta;
    };

    public abstract getRowData: (manifest: T) => RowData;

    public getLatestVersion = (): string => {
        const versions = getKeys(this.manifest.versionStatusMap);
        const sorted = sort(versions, this.shouldIncludePreReleaseVersions);
        return sorted[0] ?? this.manifest.version;
    };

    public getLatestLocalVersion = (): string => {
        const versions = getKeys(this.manifest.versionStatusMap).filter(
            (version) => this.manifest.versionStatusMap[version] === "LOCAL",
        );
        return sort(versions, this.shouldIncludePreReleaseVersions)[0];
    };
}
