import type { PayloadAction } from "@reduxjs/toolkit";
import { createSlice } from "@reduxjs/toolkit";
import type { AnyAction } from "redux";
import type { RootState } from "./CMTStore";

export type AppState = {
    connectionStatus: "connected" | "disconnected";
};

const initialState: AppState = {
    connectionStatus: "disconnected",
};

export const slice = createSlice({
    name: "websockets",
    initialState: initialState,
    reducers: {
        wsConnected: (state) => {
            state.connectionStatus = "connected";
        },
        wsDisconnected: (state) => {
            state.connectionStatus = "disconnected";
        },
        wsSendData: (state, action: PayloadAction<AnyAction>) => state,
    },
    extraReducers: {},
});

export type ReducerActions = typeof slice.actions;
export type ReducerState = ReturnType<typeof slice.reducer>;

export const actions = {
    ...slice.actions,
};

export const getState = (state: RootState): ReducerState => state.websockets;

export const reducer = slice.reducer;

const URL = `ws://localhost:${
    window.location.port === "3000" ? "80" : window.location.port
}/websockets/${window.location.port === "3000" ? "request" : "cmwsapi"}`;

export class RestWindowCommunication implements WindowCommunication {
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    private responseHandler: (...args: any[]) => void = (): void =>
        console.error("Unable to handle response. No response handler registered.");

    constructor(private socket = new WebSocket(URL)) {
        // websocket handlers
        this.socket.onmessage = this.onMessage;
        this.socket.onclose = this.onClose;
        this.socket.onopen = this.onOpen;
    }

    openSocket = (): void => {
        // connect to the remote host
        this.socket = new WebSocket(URL);
        // websocket handlers
        this.socket.onmessage = this.onMessage;
        this.socket.onclose = this.onClose;
        this.socket.onopen = this.onOpen;
    };

    send = <T>(name: string, args: T[]): void => {
        switch (this.socket.readyState) {
            case WebSocket.CLOSED:
                this.responseHandler(JSON.stringify(actions.wsDisconnected()));
                this.openSocket();
                break;
            case WebSocket.OPEN:
                this.socket.send(JSON.stringify(args));
                break;
            case WebSocket.CONNECTING:
                this.waitForSocketConnection(name, args);
                break;
            default:
                console.error(`Cannot send message. Status: ${this.socket.readyState}`);
                break;
        }
    };

    waitForSocketConnection = <T>(name: string, args: T[]): void => {
        setTimeout(() => {
            if (this.socket.readyState === WebSocket.OPEN) {
                this.send(name, args);
            } else if (this.socket.readyState === WebSocket.CONNECTING) {
                this.waitForSocketConnection(name, args);
            } else {
                console.log("Websocket Connection could not be opened");
            }
        }, 5);
    };

    registerFunction = <T>(name: string, fn: (...args: T[]) => void): void => {
        this.responseHandler = fn;
    };
    deregisterFunction = (name: string): void => {
        /* Do nothing */
    };

    onOpen = (): void => {
        console.log("websocket open");
        this.responseHandler(JSON.stringify(actions.wsConnected()));
    };

    onClose = (): void => {
        console.log("websocket closed");
        this.responseHandler(JSON.stringify(actions.wsDisconnected()));
    };

    onMessage = (event: { data: string }): void => {
        // console.log("receiving server message");
        const arr: unknown = JSON.parse(event.data);
        this.responseHandler((arr as string[])[0]);
    };
}
