import type { SCFConfiguration } from "@microchip/scf-common/lib/PackageJsonConfiguration";
import { Content } from "../BaseContent/Content";
import type { RowData } from "../types/RowData";
import type { ClassicManifest } from "./ClassicManifest";

export class ClassicContent extends Content<ClassicManifest> {
    public getRowData = (manifest: ClassicManifest): RowData => {
        const name = manifest.meta?.name ?? manifest.name ?? manifest.title;
        return {
            id: this.id,
            moduleName: name,
            moduleVersion: manifest.version,
            moduleDescription: manifest.meta?.description ?? "",
            moduleCategory: manifest.meta?.category ?? manifest.section,
            packageName: manifest.name,
            help: getHelp(manifest),
            versions: manifest.versionStatusMap,
            status: this.getSelectedStatus(),
            readmeUrl: manifest.readmeUrl,
            versionReadMeUrlMap: manifest.versionReadMeUrlMap,
            meta: manifest.meta,
            compatibilityErrors: manifest.versionCompatibilityErrorMap,
        };
    };
}

function getHelp(manifest: ClassicManifest): SCFConfiguration["help"] | undefined {
    if (manifest.versionLinkMap) {
        return {
            tooltip: `${manifest.name} v${manifest.version} Release Notes`,
            url: manifest.versionLinkMap?.[manifest.version]?.releaseNotes ?? "",
        };
    }
    return undefined;
}
