import React from "react";
import * as Hooks from "../../../redux/hooks";
import { SourceSetting } from "./SourceSetting";
import { actions, getState } from "../../ContentManager/contentManagerSlice";

const SourceSettings = (): JSX.Element => {
    const dispatch = Hooks.useAppDispatch();
    const state = getState(Hooks.useAppSelector((state) => state));
    const [registries, setRegistries] = React.useState(state.registries);
    React.useEffect(() => {
        setRegistries(state.registries);
    }, [state.registries]);

    return (
        <>
            <SourceSetting
                title="MCC Content Registry(s)"
                description="This allows users to see content from various sources. Should be
                modified only when you have multiple registries available,
                otherwise keep it as default for all types of MCC content to show
                up. Enter source registries as a comma seperated list."
                askForConfirmation={false}
                onSubmit={(newSource: string): void => {
                    dispatch(actions.requestSetRegistries(newSource.split(",")));
                }}
                onChange={(newSource: string): void => {
                    setRegistries(newSource.split(","));
                }}
                source={registries.join(",")}
                defaultSource={state.defaultRegistries?.join(",")}
                isApplyDisabled={(newSource: string): boolean => {
                    return newSource === state.registries.join(",");
                }}
                disableTextBox={false}
            />
        </>
    );
};

export default SourceSettings;
