import { useRef } from "react";
import ReactMarkdown from "react-markdown";
import { Button } from "primereact/button";
import { actions } from "../../redux/CMTStore";
import * as Hooks from "../../redux/hooks";
import type { DialogProps } from "primereact/dialog";
import { Dialog } from "primereact/dialog";
import { Toast } from "primereact/toast";
import "./scss/shutdownNotice.scss";
import "../../primereact-override.scss";
import { prerelease } from "semver";

export type DownloadFailure = {
    label: string;
    version: string;
};

export const DISPLAY_DOWNLOAD_FAILURE_NOTICE = "displayDownloadFailureNotice";
export type DisplayDownloadFailureNotice = {
    type: typeof DISPLAY_DOWNLOAD_FAILURE_NOTICE;
    failedDownloads: DownloadFailure[];
    shutdownAfter: boolean;
};

export const GENERIC_SHUTDOWN_NOTICE = "generic-shutdown-notice";
export type GenericShutdownNotice = {
    type: typeof GENERIC_SHUTDOWN_NOTICE;
    title?: string;
    body: string;
};

export const GENERIC_SERVER_MESSAGE = "generic-server-message";
export type GenericServerMessage = {
    type: typeof GENERIC_SERVER_MESSAGE;
    severity: string;
    shutdownAfter: boolean;
    title?: string;
    body: string;
};

export type Props =
    | GenericShutdownNotice
    | DisplayDownloadFailureNotice
    | GenericServerMessage;

const renderCloseFooter = (onClose: () => void) => (): JSX.Element =>
    (
        <div>
            <Button
                label="OK"
                icon="pi pi-check"
                className="p-button-info"
                onClick={onClose}
                autoFocus
            ></Button>
        </div>
    );

const renderShutdownNoticeFooter = (onShutdown: () => void) => (): JSX.Element =>
    (
        <div>
            <Button
                label="Close MCC"
                icon="pi pi-sign-out"
                className="p-button-danger"
                onClick={onShutdown}
                autoFocus
            />
        </div>
    );

export const ShutdownNotice = (props: Props): JSX.Element => {
    const dispatch = Hooks.useAppDispatch();
    const toast = useRef<Toast>(null);

    const acceptShutdown = (): void => {
        toast?.current?.show({
            severity: "info",
            summary: "Confirmed",
            detail: "MCC will now exit.",
            life: 3000,
        });
        dispatch(actions.requestShutdown());
    };

    const dialogProps: DialogProps = {
        position: "center",
        style: { width: "50vw" },
        footer: renderShutdownNoticeFooter(acceptShutdown),
        onHide: () => dispatch(actions.clearShutdownNotice()),
        draggable: false,
        resizable: false,
        visible: true,
        modal: true,
    };

    switch (props.type) {
        case GENERIC_SERVER_MESSAGE:
            if (!props.shutdownAfter) {
                dialogProps.footer = renderCloseFooter(() =>
                    dispatch(actions.clearShutdownNotice()),
                );
            }
            return (
                <div>
                    <Dialog
                        {...dialogProps}
                        header={(): JSX.Element => <>{props.title ?? "Error"}</>}
                    >
                        <ReactMarkdown children={props.body} />
                    </Dialog>
                </div>
            );
        case GENERIC_SHUTDOWN_NOTICE:
            return (
                <div>
                    <Dialog
                        {...dialogProps}
                        header={(): JSX.Element => <>{props.title ?? "Error"}</>}
                    >
                        <p className="p-m-0">{props.body}</p>
                    </Dialog>
                    <Toast ref={toast} />
                </div>
            );
        case DISPLAY_DOWNLOAD_FAILURE_NOTICE:
            if (!props.shutdownAfter) {
                dialogProps.footer = renderCloseFooter(() =>
                    dispatch(actions.clearShutdownNotice()),
                );
            } else {
                dialogProps.onHide = acceptShutdown;
            }
            return (
                <div>
                    <Dialog
                        {...dialogProps}
                        header={(): JSX.Element => <>Download Failure</>}
                    >
                        <p className="p-m-0">
                            Unable to download the following packages. See log for
                            details.
                        </p>
                        <ul>
                            {props.failedDownloads.map((x) => (
                                <div
                                    className={"failed-download-container"}
                                    key={x.label}
                                >
                                    <i className={"pi pi-exclamation-circle"} />
                                    <li className={"failed-download-elem"} id={x.label}>
                                        {x.label}
                                        {prerelease(x.version) !== null
                                            ? " (pre-release version)"
                                            : ""}
                                    </li>
                                </div>
                            ))}
                        </ul>
                    </Dialog>
                    <Toast ref={toast} />
                </div>
            );
    }
};
