import type { PayloadAction } from "@reduxjs/toolkit";
import { createSlice } from "@reduxjs/toolkit";
import type { RootState } from "../../redux/CMTStore";
import type {
    ContentIdBundle,
    ContentTypes,
    ExpandedKeys,
    WizardSteps,
    LocalContentStatus,
    ReleaseNotesBundle,
} from "../types";

export type FeaturedContent = Record<string, ContentTypes[]>;

export type WizardState = {
    expandedKeys: ExpandedKeys;
    supportedContentTypes: ContentTypes[];
    contentType?: ContentTypes;
    localContentStatus: LocalContentStatus;
    wizardStep: WizardSteps;
    releaseNotes?: ReleaseNotesBundle;
    featuredContent?: FeaturedContent;
};

const initialState: WizardState = {
    expandedKeys: {},
    supportedContentTypes: ["MELODY", "CLASSIC", "HARMONY", "TRIDION"],
    localContentStatus: "NO_CONTENT_DOWNLOADED",
    wizardStep: "CONTENT_SELECTOR",
};

export const slice = createSlice({
    name: "wizard",
    initialState: initialState,
    reducers: {
        setSupportedContentTypes: (state, action: PayloadAction<ContentTypes[]>) => {
            state.supportedContentTypes = action.payload;
        },
        setExpandedKeys: (state, action: PayloadAction<ExpandedKeys>) => {
            state.expandedKeys = action.payload;
        },
        setContentType: (state, action: PayloadAction<ContentTypes>) => {
            state.contentType = action.payload;
            if (state.wizardStep === "CONTENT_SELECTOR") {
                state.wizardStep = "TABLE";
            }
        },
        setLocalContentStatus: (state, action: PayloadAction<LocalContentStatus>) => {
            state.localContentStatus = action.payload;
        },
        setWizardStep: (state, action: PayloadAction<WizardSteps>) => {
            state.wizardStep = action.payload;
        },
        requestWizardComplete: (state, action: PayloadAction<ContentIdBundle>) => {
            console.log("Wizard Complete!");
        },
        requestReleaseNotes: () => {
            console.log("Requesting release notes");
        },
        setReleaseNotes: (state, action: PayloadAction<ReleaseNotesBundle>) => {
            state.releaseNotes = action.payload;
        },
        requestFeaturedContent: () => {
            console.log("Requesting featuredContent");
        },
        setFeaturedContent: (
            state,
            action: PayloadAction<WizardState["featuredContent"]>,
        ) => {
            state.featuredContent = action.payload;
        },
    },
    extraReducers: {},
});

export type ReducerActions = typeof slice.actions;
export type ReducerState = ReturnType<typeof slice.reducer>;

export const actions = {
    ...slice.actions,
};

export const getState = (state: RootState): ReducerState => state.wizard;

export const reducer = slice.reducer;
