import help from "../../resources/help.png";
import React from "react";

interface Props {
    onClick: () => void;
    altText?: string;
    className?: string;
    title?: string;
}

export const HelpIcon = (props: Props): JSX.Element => {
    return (
        <img
            alt={props.altText ?? "Help"}
            src={help}
            className={props.className}
            onClick={props.onClick}
            title={props.title}
        />
    );
};
