import type { ContentTypes } from "../../types";
import * as React from "react";
import { Card } from "primereact/card";
import type { DeviceSupport } from "./DeviceSupport";
import { HelpIcon } from "../../common/HelpIcon";

const headerToolTips = {
    MELODY: "Click to Open MCC Melody Help",
    CLASSIC: "Click to Open MCC Classic Help",
    HARMONY: "Click to Open MPLAB Harmony Help",
    TRIDION: "",
};

const getExplanationElements = (explanations: string[]): JSX.Element[] => {
    return explanations.map((exp, i) => (
        <li key={`why${i}`} className={"why-elem"}>
            <span>{exp}</span>
        </li>
    ));
};

const capitalizeOnlyFirstLetter = (word: string): string =>
    word.charAt(0).toUpperCase() + word.toLowerCase().slice(1);

const getHeaderText = (contentType: ContentTypes): string => {
    const preFix = contentType === "HARMONY" ? "MPLAB " : "MCC ";
    return `${preFix}${capitalizeOnlyFirstLetter(contentType)}`;
};

const getHeaderHelpIcon = (
    contentType: ContentTypes,
    referenceLink: string,
): JSX.Element => (
    <span className={`card-header-link`}>
        <HelpIcon
            onClick={(): void => {
                window.open(referenceLink, "_blank");
            }}
            altText={"Technical Reference"}
            title={headerToolTips[contentType]}
        />
    </span>
);

const getHeader = (
    contentType: ContentTypes,
    isDisabled: boolean,
    referenceLink: string,
): JSX.Element => {
    return (
        <>
            <span className={`card-header-text ${isDisabled ? "card-disabled" : ""}`}>
                {getHeaderText(contentType)}
            </span>
            {getHeaderHelpIcon(contentType, referenceLink)}
        </>
    );
};

const createBody = (quickExplanations: string[], isDisabled: boolean): JSX.Element => {
    return (
        <div className={`card-body${isDisabled ? " card-disabled" : ""}`}>
            <ul className={"explain-list"}>
                {getExplanationElements(quickExplanations)}
            </ul>
        </div>
    );
};

export const createContentCard = (
    contentType: ContentTypes,
    quickExplanations: string[],
    deviceSupport: DeviceSupport,
    createContentButton: (contentType: ContentTypes) => JSX.Element,
    isDisabled: boolean,
    referenceLink: string,
): JSX.Element => {
    return (
        <Card
            header={getHeader(contentType, isDisabled, referenceLink)}
            footer={createContentButton(contentType)}
            className={`content-card${isDisabled ? " card-disabled" : ""}`}
        >
            {createBody(quickExplanations, isDisabled)}
        </Card>
    );
};
