import * as React from "react";
import type { MyTreeNode } from "../../../../../../model/types/TreeNode";
import { BrightnessHigh, Cloud } from "react-bootstrap-icons";
import styles from "./scss/status.module.scss";
import type { ModuleStatus } from "../../../../../../model/types/RowData";

export type Props = {
    node: MyTreeNode;
    onClick: () => void;
};

export const StatusColumn: React.FC<Props> = (props): JSX.Element => {
    const toTitleCase = (text: string): string => {
        return text.substr(0, 1).toUpperCase() + text.substr(1).toLowerCase();
    };
    const getTooltipText = (status: ModuleStatus | undefined): string => {
        if (status === "AVAILABLE") {
            return "Component available for download";
        } else if (status === "UPDATE") {
            return "New Versions Available";
        }
        return "Latest released version selected";
    };
    const getColumn = (
        status: ModuleStatus | undefined,
        version: string | undefined,
    ): JSX.Element => {
        const tooltipText = getTooltipText(status);
        if (status && version) {
            return (
                <span
                    className={`${styles[`status-${status.toLowerCase()}`]}`}
                    title={tooltipText}
                >
                    {toTitleCase(status)}
                </span>
            );
        }
        if (status === "UPDATE") {
            return (
                <span title={tooltipText} onClick={props.onClick}>
                    <Cloud className={"status-icon out-of-date"} />
                </span>
            );
        }
        if (status === "AVAILABLE") {
            return (
                <span title={tooltipText} onClick={props.onClick}>
                    <BrightnessHigh className={"status-icon out-of-date"} />
                </span>
            );
        }
        return (
            <span title={tooltipText}>
                <BrightnessHigh className={"status-icon up-to-date"} />
            </span>
        );
    };
    const status = props.node.data?.status;
    const version = props.node.data?.moduleVersion;

    return <>{getColumn(status, version)}</>;
};
