import type { MyTreeNode } from "../../../model/types/TreeNode";
import { getAllLeavesUnderCategory, getLeaves } from "../../../model/types/TreeNode";
import { values } from "../../../util/objects";
import type { UsedContent } from "../contentManagerSlice";

export const removeRemoteOnlyContent = (leaves: MyTreeNode[]): MyTreeNode[] => {
    return leaves.filter((leaf) => {
        return values(leaf.data?.versions).includes("LOCAL");
    });
};
export const getLocalContentOnly = (table: MyTreeNode[]): MyTreeNode[] => {
    let newTable: MyTreeNode[] = [];
    table.forEach((row) => {
        const leaves: MyTreeNode[] = getLeaves(row);
        const localOnly: MyTreeNode[] = removeRemoteOnlyContent(leaves);
        newTable = [...newTable, ...localOnly];
    });
    return newTable;
};

export const getUsedContentOnly = (
    table: MyTreeNode[],
    usedContent: UsedContent[],
): MyTreeNode[] => {
    let newTable: MyTreeNode[] = [];
    const usedContentKeys = usedContent.map((used) => used.name);
    table.forEach((row) => {
        const leaves: MyTreeNode[] = getLeaves(row);
        leaves.forEach((leaf) => {
            const key = leaf.key;
            if (key) {
                if (usedContentKeys.includes(key.toString())) {
                    newTable = [...newTable, leaf];
                }
            }
        });
    });
    return newTable;
};

/**
 * Constructs a tree structure from a list of tree leaves by organizing leaves
 * in categories.
 * @param leaves
 * @param referenceTree provides a tree which has the same structure as the tree you are creating.
 */
export function buildTree(
    leaves: MyTreeNode[],
    referenceTree: MyTreeNode[],
): MyTreeNode[] {
    const newTree: MyTreeNode[] = [];
    leaves.forEach((leaf, index) => {
        const cat = leaf.data?.moduleCategory;
        let leavesUnderCat: MyTreeNode[];
        if (cat === "" || cat) {
            let catArr = cat.split("/");
            let newNode: MyTreeNode | undefined;
            if (cat === "") {
                catArr = ["children"];
                newNode = getCopyOfNode(
                    referenceTree.find((node) => node.key === leaf.key),
                );
            } else {
                catArr.push("children");
                newNode = getCopyOfNode(referenceTree.find((node) => node.key === cat));
            }
            leavesUnderCat = getAllLeavesUnderCategory(cat, leaves);
            if (newNode) {
                catArr.unshift();
                newNode.children = leavesUnderCat;
                if (!newTree.find((node) => node.key === newNode?.key)) {
                    newTree.push(newNode);
                }
            }
        }
    });
    return newTree;
}
function getCopyOfNode(node?: MyTreeNode): MyTreeNode | undefined {
    return node ? { ...node } : undefined;
}
