import type { PayloadAction } from "@reduxjs/toolkit";
import { createSlice } from "@reduxjs/toolkit";
import type { RootState } from "../../redux/CMTStore";

export type ToolbarState = {
    supportedDevices: string[];
    selectedDevice: string;
};

const initialState: ToolbarState = {
    supportedDevices: ["PIC18F57Q43"],
    selectedDevice: "PIC18F57Q43",
};

export const slice = createSlice({
    name: "developer",
    initialState: initialState,
    reducers: {
        setSupportedDevices: (state, action: PayloadAction<string[]>) => {
            state.supportedDevices = action.payload;
        },
        setSelectedDevice: (state, action: PayloadAction<string>) => {
            state.selectedDevice = action.payload;
        },
        requestSetSelectedDevice: (state, action: PayloadAction<string>) => {
            console.log("Request Set Selected Device: " + action.payload);
        },
        requestSetIncludePreReleaseVersions: (state, action: PayloadAction<string>) => {
            console.log("Sending Set Include Pre-release Versions");
        },
        requestServerReset: (state) => {
            console.log("Sending Server Reset Request");
        },
        reset: (state) => {
            console.log("Resetting View");
        },
    },
    extraReducers: {},
});

export type ReducerActions = typeof slice.actions;
export type ReducerState = ReturnType<typeof slice.reducer>;

export const actions = {
    ...slice.actions,
};

export const getState = (state: RootState): ReducerState => state.developer;

export const reducer = slice.reducer;
