import { Button } from "primereact/button";
import React from "react";
import { ConfirmDialog } from "../../common/ConfirmDialog";

interface SourceProps {
    title: string;
    description: string;
    onSubmit: (newSource: string) => void;
    onChange: (newSource: string) => void;
    isApplyDisabled: (newSource: string) => boolean;
    askForConfirmation: boolean;
    source: string;
    defaultSource: string | undefined;
    disableTextBox: boolean;
}

interface SourceState {
    showConfirmationDialog: boolean;
}

/**
 * Generate form for setting one or more sources.
 * Used in Settings page for setting NPM registries or Git repositories
 */
export class SourceSetting extends React.Component<SourceProps, SourceState> {
    constructor(props: SourceProps) {
        super(props);
        this.state = {
            showConfirmationDialog: false,
        };
    }

    render(): JSX.Element {
        const value = this.props.source;
        return (
            <form
                className={"settings-form"}
                onSubmit={(e): void => {
                    e.preventDefault();
                    if (this.props.askForConfirmation) {
                        this.setState({ showConfirmationDialog: true });
                    } else {
                        this.props.onSubmit(value);
                    }
                }}
            >
                {this.state.showConfirmationDialog ? (
                    <ConfirmDialog
                        title="Warning"
                        description={[
                            "Downloading content from a new source will remove content that you have downloaded previously.",
                        ]}
                        okButtonLabel="Continue"
                        onOk={(): void => {
                            this.setState({ showConfirmationDialog: false });
                            this.props.onSubmit(value);
                        }}
                        onCancel={(): void => {
                            this.setState({ showConfirmationDialog: false });
                        }}
                    />
                ) : null}
                <div className="row">
                    <div className="col mt-2">
                        <label htmlFor="source-input">
                            <b>{this.props.title}</b>
                        </label>
                    </div>
                    <div className="col-12">{this.props.description}</div>
                </div>
                <div className="row">
                    <div className="col-12 mt-2">
                        <input
                            className="w-100"
                            id="source-input"
                            type={"text"}
                            value={value}
                            onChange={(e): void => {
                                this.props.onChange(e.target.value);
                            }}
                            disabled={this.props.disableTextBox}
                        />
                    </div>
                </div>
                <div className="row flex-wrap mt-2">
                    <div className="col-12">
                        <Button
                            className="me-3"
                            icon="pi pi-check"
                            label="Apply"
                            type={"submit"}
                            disabled={this.props.isApplyDisabled(value)}
                        />
                        <Button
                            className="bg-secondary"
                            icon="pi pi-undo"
                            label="Revert to defaults"
                            onClick={(e): void => {
                                e.preventDefault();
                                if (this.props.defaultSource) {
                                    this.props.onChange(this.props.defaultSource);
                                }
                            }}
                            disabled={value === this.props.defaultSource}
                        />
                    </div>
                </div>
            </form>
        );
    }
}
