import * as Hooks from "../../redux/hooks";
import { actions } from "../../redux/CMTStore";
import { ConfirmDialog } from "./ConfirmDialog";

export type displayServerConfirmationProps = {
    id: string;
    title: string;
    body: Array<string>;
};

export type sendServerConfirmationProps = {
    id: string;
    response: boolean;
};
export const ServerConfirmation = (
    props: displayServerConfirmationProps,
): JSX.Element => {
    const dispatch = Hooks.useAppDispatch();
    return (
        <ConfirmDialog
            title={props.title}
            description={props.body}
            okButtonLabel="Okay"
            onOk={(): void => {
                dispatch(
                    actions.requestSetServerConfirmationResponse({
                        id: props.id,
                        response: true,
                    }),
                );
                dispatch(actions.displayServerConfirmationMsg(undefined));
            }}
            onCancel={(): void => {
                dispatch(
                    actions.requestSetServerConfirmationResponse({
                        id: props.id,
                        response: false,
                    }),
                );
                dispatch(actions.displayServerConfirmationMsg(undefined));
            }}
        />
    );
};
