import type { MenuItem } from "primereact/menuitem/menuitem";

export interface OptionProps {
    onExpandAll?: () => void;
    onCollapseAll?: () => void;
}

export type OptionItem = {
    label: string;
    icon: string;
    command: (e: React.ChangeEvent<HTMLInputElement>) => void;
};

export const getOptionItems = (props: OptionProps): MenuItem[] => {
    return [
        {
            label: "Expand All",
            icon: "pi pi-chevron-down",
            command: (): void => {
                props.onExpandAll?.();
            },
        },
        {
            label: "Collapse All",
            icon: "pi pi-chevron-right",
            command: (): void => {
                props.onCollapseAll?.();
            },
        },
    ];
};
