import type { MyTreeNode } from "../types/TreeNode";
import { indexOf, sortBy } from "lodash";

const getLabel = (node: MyTreeNode): string => node?.data?.moduleName ?? "";
const alphabetically = (a: MyTreeNode, b: MyTreeNode): number =>
    getLabel(a).localeCompare(getLabel(b));

const NOT_FOUND = -1;
const COMPONENT_ORDER = [
    "Libraries",
    "Drivers",
    "PLIB",
    "Hardware Peripherals",
    "System",
    "Devices",
];

export const sortTree = (nodes: MyTreeNode[]): MyTreeNode[] => {
    nodes.forEach((node) => node.children.sort(alphabetically));

    return sortBy(nodes, ({ key }) => {
        const index = indexOf(COMPONENT_ORDER, key);
        return index === NOT_FOUND ? nodes.length : index;
    });
};
