import { Button } from "primereact/button";
import { InputText } from "primereact/inputtext";
import React from "react";
import styles from "./scss/globalFilter.module.scss";

interface SearchTermInputTextBoxProps {
    onSearchTermEntered: (searchTerm: string) => void;
}

const SearchTermInputTextBox: React.FC<SearchTermInputTextBoxProps> = (props) => {
    const ENTER_KEY_CODE = "Enter";
    const [inputValue, setInputValue] = React.useState("");

    const onInputValueChanged = (value: string): void => {
        setInputValue(value);
    };

    const clearInputTextBox = (): void => {
        setInputValue("");
    };

    const onKeyPress = (event: React.KeyboardEvent<HTMLInputElement>): void => {
        const searchTerm = inputValue.trim();
        if (event.key === ENTER_KEY_CODE && searchTerm) {
            event.preventDefault();
            props.onSearchTermEntered(inputValue);
            clearInputTextBox();
        }
    };

    const onCloseButtonClicked = (): void => {
        clearInputTextBox();
    };

    return (
        <div className={styles.inputTextBox}>
            <div className="p-input-icon-left">
                <i className="pi pi-search" />
                <InputText
                    id={styles.globlSearchInputElement}
                    value={inputValue}
                    onChange={(e: React.ChangeEvent<HTMLInputElement>): void =>
                        onInputValueChanged(e.target.value)
                    }
                    onKeyPress={onKeyPress}
                    placeholder="Type to Search Globally..."
                />
                {inputValue && (
                    <Button
                        icon="pi pi-times"
                        className={`p-button-rounded p-button-secondary p-button-text ${styles.clearInputTextBox}`}
                        onClick={onCloseButtonClicked}
                    />
                )}
            </div>
        </div>
    );
};

export default SearchTermInputTextBox;
