import React from "react";
import { gte } from "semver";
import * as Hooks from "../../../redux/hooks";
import { getState } from "../../ContentManager/contentManagerSlice";
import CheckMark from "./CheckMark";
import UpdateStatus from "./UpdateSatus";

const ContentManagerUpdate = (): JSX.Element => {
    const state = getState(Hooks.useAppSelector((state) => state));
    const currentVersion = state.currentVersion;
    const latestRemoteVersion = state.latestContentManagerVersion ?? currentVersion;
    const updateStatus = state.contentManagerUpdateStatus;
    const isContentManagerUpToDate =
        gte(currentVersion, latestRemoteVersion) ||
        process.env.NODE_ENV === "development";
    const isDownloading = updateStatus === "loading" && !isContentManagerUpToDate;

    return (
        <div className="row border pb-2">
            <div className="col mt-2">
                <span>
                    <b>Content Manager</b>
                </span>
            </div>
            <div className="col-12 d-flex">
                <CheckMark updateStatus={updateStatus} isLoading={isDownloading} />
                <UpdateStatus
                    libraryName="Content Manager"
                    updateStatus={updateStatus}
                    currentVersion={currentVersion}
                    latestVersion={latestRemoteVersion}
                    isUpToDate={isContentManagerUpToDate}
                    releaseNotesUrl={
                        "https://www.npmjs.com/package/@mchp-mcc/content-manager"
                    }
                />
            </div>
        </div>
    );
};

export default ContentManagerUpdate;
