import React from "react";
import { Chips } from "primereact/chips";
import { Button } from "primereact/button";
import styles from "./scss/globalFilter.module.scss";

interface SearchTermCreatorProps {
    globalSearchedTerms: string[];
    setGlobalSearchedTerms: (searchedTerms: string[]) => void;
}

const SearchTermsDisplay: React.FC<SearchTermCreatorProps> = (props) => {
    const onClearButtonClicked = (): void => {
        props.setGlobalSearchedTerms([]);
    };

    return (
        <div className={styles.searchTermsDisplay}>
            <Chips
                className={styles.searchTermsContainer}
                value={props.globalSearchedTerms}
                onChange={(e): void => {
                    props.setGlobalSearchedTerms(e.value);
                }}
            />
            <div className="clearAllButton">
                {props.globalSearchedTerms.length > 1 && (
                    <Button
                        label="Clear All"
                        icon="pi pi-times-circle"
                        iconPos="right"
                        className={`p-button-secondary p-button-text clearAllSearchedTerms ${styles.clearAllSearchedTerms}`}
                        onClick={onClearButtonClicked}
                    />
                )}
            </div>
        </div>
    );
};

export default SearchTermsDisplay;
