import semver, { prerelease } from "semver";

type Result = { original: string; clean: string | undefined };
type ValidResult = { original: string; clean: string };

const getSemverOptions = (shouldIncludePreReleaseVersions: boolean): semver.Options => {
    return {
        loose: true,
        includePrerelease: shouldIncludePreReleaseVersions,
    };
};

const clean = (
    version: string,
    shouldIncludePreReleaseVersions: boolean,
): string | undefined => {
    const options = getSemverOptions(shouldIncludePreReleaseVersions);
    const result = semver.clean(version, options) ?? semver.valid(semver.coerce(version));

    return result ?? undefined;
};

export const sort = (
    versions: string[],
    shouldIncludePreReleaseVersions: boolean,
): string[] => {
    const invalidVersions: string[] = [];

    const sortedVersions = versions
        .map(
            (original): Result => ({
                original,
                clean: clean(original, shouldIncludePreReleaseVersions),
            }),
        )
        .filter((result: Result): result is ValidResult => {
            const isValid: boolean = result.clean !== undefined;

            if (!isValid) {
                invalidVersions.push(result.original);
            }

            if (!shouldIncludePreReleaseVersions && prerelease(result.clean ?? "")) {
                return false;
            }

            return isValid;
        })
        .sort((a, b) => semver.rcompare(a.clean, b.clean));

    return [
        ...sortedVersions.map((result) => result.original),
        ...invalidVersions.sort((a, b) => b.localeCompare(a)),
    ];
};
