import type { Content } from "../BaseContent/Content";
import { ContentManager } from "../BaseContent/ContentModel";
import type { ClassicManifest } from "./ClassicManifest";
import { ClassicContent } from "./ClassicContent";

export class ClassicManager extends ContentManager<ClassicManifest, ClassicContent> {
    protected getNewContent = (
        name: string,
        data: ClassicManifest,
        shouldIncludePreReleaseVersions: boolean,
    ): Content<ClassicManifest> => {
        return new ClassicContent(name, data, shouldIncludePreReleaseVersions);
    };
}
