import type { Keywords } from "@mchp-mcc/scf-catalog/lib/src/Keywords";
import { groupBy, uniqBy } from "lodash";
import type { UsedContent } from "../../features/ContentManager/contentManagerSlice";
import { initialContentBundle } from "../../features/ContentManager/contentManagerSlice";
import type {
    ContentBundle,
    ContentIdBundle,
    ContentList,
    ContentTypes,
    DisplayMode,
} from "../../features/types";
import { forEach, map } from "../../util/objects";
import type { CategoryInjectorOptions } from "../BaseContent/CategorySort";
import type { Content } from "../BaseContent/Content";
import type { ContentModel } from "../BaseContent/ContentModel";
import type { ClassicContent } from "../classic/ClassicContent";
import type { ClassicManifest } from "../classic/ClassicManifest";
import { ClassicManager } from "../classic/ClassicModel";
import type { HarmonyContent } from "../harmony/HarmonyContent";
import type { HarmonyManifest } from "../harmony/HarmonyManifest";
import { HarmonyManager } from "../harmony/HarmonyModel";
import type { TridionContent } from "../tridion/TridionContent";
import type { TridionManifest } from "../tridion/TridionManifest";
import { TridionManager } from "../tridion/TridionModel";
import type { MelodyContent } from "../melody/MelodyContent";
import type { MelodyManifest } from "../melody/MelodyManifest";
import { MelodyManager } from "../melody/MelodyModel";
import type { ContentId } from "../types/ContentId";
import type { VersionStatus } from "../types/RowData";
import type { MyTreeNode } from "../types/TreeNode";
import { getKeys, values } from "../types/Utils";
import { sortTree } from "./sortTree";

export type ContentModelList = ContentBundle<
    ContentModel<MelodyManifest, MelodyContent>,
    ContentModel<ClassicManifest, ClassicContent>,
    ContentModel<HarmonyManifest, HarmonyContent>,
    ContentModel<TridionManifest, TridionContent>
>;

export type ManagerProps = {
    data: ContentList;
    availableData: ContentIdBundle;
    selectedVersions: ContentId[];
    categorySort: CategoryInjectorOptions;
    displayMode: DisplayMode;
    usedVersions: UsedContent[];
    shouldIncludePreReleaseVersions: boolean;
};

const toContentIds = (x: UsedContent[]): ContentId[] =>
    x.map((value) => ({
        name: value.name,
        version: value.version,
    }));

const combine = (a: ContentId[], b: ContentId[], c?: ContentId[]): ContentId[] => {
    const combined: Record<string, ContentId> = {};
    a.forEach((id) => (combined[id.name] = id));
    b.forEach((id) => (combined[id.name] = id));
    c?.forEach((id) => (combined[id.name] = id));
    return values(combined);
};

const equals = (a: ContentId, b: ContentId): boolean =>
    a.name === b.name && a.version === b.version;

const isContainedIn =
    (list: ContentId[]) =>
    (x: ContentId): boolean =>
        list.find((i) => equals(i, x)) != null;

const combineDuplicatesByCategory = (tableData: MyTreeNode[]): MyTreeNode[] => {
    const groupedTableData = groupBy(tableData, "key");

    const mergedTableData = Object.keys(groupedTableData).map((key) => {
        const currentCategory = groupedTableData[key];
        const children = uniqBy(
            currentCategory.flatMap((node) => node.children),
            "key",
        );

        return { ...currentCategory[0], children: children };
    });

    return mergedTableData;
};

const combineDuplicatesByKey = (tableData: MyTreeNode[]): MyTreeNode[] => {
    const nodes: MyTreeNode[] = [];
    tableData.forEach((node) => {
        const nodeExists = nodes.find((childNode) => {
            return childNode.key === node.key;
        });

        if (nodes.length === 0 || !nodeExists) {
            nodes.push(node);
        } else {
            const categoryIndex = nodes.findIndex(
                (childNode) => childNode.key === node.key,
            );

            if (categoryIndex !== -1) {
                nodes[categoryIndex].children = [
                    ...nodes[categoryIndex].children,
                    ...node.children,
                ];
            }
        }
    });
    return nodes;
};

const includesContentType = (
    contentType: ContentTypes,
    keywords?: Keywords[],
): boolean => {
    const contentTypeKeyword = contentType.toLowerCase() as Keywords;
    const metaKeywords = keywords ?? [];
    return metaKeywords.length > 0 ? metaKeywords.includes(contentTypeKeyword) : true;
};

const filterRowsByCategory = (
    contentType: ContentTypes,
    tableData: MyTreeNode[],
): MyTreeNode[] => {
    const nodes: MyTreeNode[] = [];

    tableData.forEach((node: MyTreeNode) => {
        const children = node.children.filter((childNode) =>
            includesContentType(contentType, childNode.data?.meta?.keywords),
        );

        if (children.length > 0) {
            nodes.push({
                ...node,
                children: children,
            });
        }
    });

    return nodes;
};

export class RemoteContentManager {
    private props: ManagerProps;
    private contentList: ContentModelList;

    constructor(managerProps: ManagerProps) {
        this.props = managerProps;

        const categoryInjector =
            managerProps.displayMode === "WIZARD" ? "Default" : managerProps.categorySort;

        this.contentList = {
            MELODY: new MelodyManager(categoryInjector),
            CLASSIC: new ClassicManager(categoryInjector),
            HARMONY: new HarmonyManager(categoryInjector),
            TRIDION: new TridionManager(categoryInjector),
        };

        getKeys(this.contentList).forEach((type) => {
            this.contentList[type as ContentTypes]?.initializeContent(
                this.props.data[type as ContentTypes],
                this.props.selectedVersions,
                this.props.availableData[type as ContentTypes],
                this.props.shouldIncludePreReleaseVersions,
            );
        });
    }

    public hasNewSelections = (): boolean => {
        const availableData = values(this.props.availableData).flat();

        const selectedAvailableData = this.props.selectedVersions.filter(
            isContainedIn(availableData),
        );

        return selectedAvailableData.length !== availableData.length;
    };

    public getSelectedModulesWithAvailableUpdates = (
        contentIds: ContentId[],
    ): ContentId[] =>
        values(this.contentList).flatMap((model) =>
            model.getContentWithUpdates(contentIds),
        );

    public getModulesWithAvailableUpdates = (): ContentId[] => {
        const contentList: ContentId[] = [];
        getKeys(this.contentList).forEach((contentName) => {
            contentList.push(
                ...values(this.contentList[contentName as ContentTypes].model)
                    .filter((mod) => mod.getSelectedStatus() === "UPDATE")
                    .map((mod) => ({ name: mod.id, version: mod.getLatestVersion() })),
            );
        });
        return contentList;
    };

    public getVersionStatus = (
        id: string,
        version: string,
    ): VersionStatus | undefined => {
        let status: VersionStatus | undefined;
        getKeys(this.contentList).forEach((contentName) => {
            if (!status) {
                status = this.contentList[contentName as ContentTypes].getVersionStatus(
                    id,
                    version,
                );
            }
        });
        return status;
    };

    public getOptionalRemoteData = (contentType?: ContentTypes): MyTreeNode[] => {
        const availableData = values(this.props.availableData).flat();

        const optionalRemoteData: MyTreeNode[] = map(this.contentList, (key, model) => {
            return model.getOptionalRemoteData(availableData);
        }).flatMap((list) => list);

        let filteredResult: MyTreeNode[] = combineDuplicatesByKey(optionalRemoteData);

        if (contentType !== undefined) {
            filteredResult = filterRowsByCategory(contentType, filteredResult);
        }

        return sortTree(filteredResult);
    };

    public getMissingRequiredContentTreeData = (
        contentType?: ContentTypes,
    ): MyTreeNode[] =>
        this.getRequiredRemoteData(this.getMissingRequiredContent(), contentType);

    public getRequiredRemoteData = (
        content: ContentId[],
        contentType?: ContentTypes,
    ): MyTreeNode[] => {
        let contentRowData: MyTreeNode[] = map(this.contentList, (key, model) => {
            return model.getRequiredRemoteData(content);
        }).flatMap((list) => list);

        if (contentType !== undefined) {
            contentRowData = filterRowsByCategory(contentType, contentRowData);
        }

        return sortTree(contentRowData);
    };

    public getAllSelectedVersions = (): ContentIdBundle => {
        const contentMap: ContentIdBundle = { ...initialContentBundle };

        getKeys(this.contentList).forEach((contentName) => {
            contentMap[contentName as ContentTypes] =
                this.contentList[contentName as ContentTypes].getSelectedVersions();
        });
        return contentMap;
    };

    public getAvailableSelectedVersions = (): ContentIdBundle => {
        const contentMap: ContentIdBundle = { ...initialContentBundle };
        forEach(this.props.availableData, (contentName, contentList) => {
            const filteredList =
                this.contentList[contentName as ContentTypes].getDefaultModules(
                    contentList,
                );

            if (filteredList.length > 0) {
                this.props.selectedVersions.forEach((id) => {
                    const existingData = filteredList.findIndex(
                        (v) => v.name === id.name,
                    );
                    if (filteredList[existingData]) {
                        //Choose selected version
                        filteredList[existingData] = id;
                    } else {
                        filteredList.push(id);
                    }
                });
            }
            contentMap[contentName as ContentTypes] = filteredList;
        });

        return contentMap;
    };

    public getRequiredContent = (updatedRequirements?: ContentId[]): ContentId[] => {
        return combine(
            values(this.getAvailableSelectedVersions()).flat(),
            toContentIds(this.props.usedVersions),
            updatedRequirements,
        );
    };

    public getMissingRequiredContent = (): ContentId[] => {
        const missingRequiredContent = this.getRequiredContent().filter((id) => {
            return this.getVersionStatus(id.name, id.version ?? "") !== "LOCAL";
        });
        return missingRequiredContent;
    };

    public getContentForDownload = (): ContentIdBundle => {
        return this.getContentIdBundleForDownload(
            combine(
                toContentIds(this.props.usedVersions),
                values(this.getAvailableSelectedVersions()).flat(),
            ),
        );
    };

    public getLatestVersions = (contentIds?: ContentId[]): ContentId[] => {
        const contentList: ContentId[] = [];
        getKeys(this.contentList).forEach((contentName) => {
            contentList.push(
                ...this.contentList[contentName as ContentTypes].getLatestVersions(
                    contentIds,
                ),
            );
        });
        return contentList;
    };

    public getContentIdBundleForDownload = (contentIds: ContentId[]): ContentIdBundle => {
        const contentMap: ContentIdBundle = { ...initialContentBundle };
        getKeys(this.contentList).forEach((contentName) => {
            const models = this.contentList[contentName as ContentTypes];
            contentMap[contentName as ContentTypes] = models.resolveIds(contentIds);
        });
        return contentMap;
    };

    public getProcessedData = (): MyTreeNode[] => {
        const processedData: MyTreeNode[] = values(this.contentList).flatMap((model) =>
            model.getProcessedData(),
        );

        return sortTree(combineDuplicatesByCategory(processedData));
    };

    public getAvailableData = (): MyTreeNode[] => {
        const availableData: MyTreeNode[] = map(this.contentList, (key, model) => {
            return model.getAvailableData(this.props.availableData[key as ContentTypes]);
        }).flatMap((list) => list);

        return sortTree(availableData);
    };

    public isAvailableContentLocal = (contentIds: ContentId[]): boolean => {
        let isContentLocal = true;
        values(this.contentList).forEach((model) => {
            if (!isContentLocal) return;
            const models = model.getModels(contentIds);
            models.forEach((content: Content) => {
                if (isContentLocal) {
                    isContentLocal =
                        content.getVersionStatus(content.getSelectedVersion()) ===
                        "LOCAL";
                }
            });
        });
        return isContentLocal;
    };
}
