import { combineReducers } from "@reduxjs/toolkit";
import type { Dispatch } from "react";
import * as contentSlice from "../features/ContentManager/contentManagerSlice";
import * as wizardSlice from "../features/wizard/wizardSlice";
import * as appSlice from "../appSlice";
import * as websocketSlice from "./socketMiddlewareSlice";
import * as developerSlice from "../features/developerToolbar/developerToolbarSlice";
import * as jobsSlice from "../features/Jobs/jobsSlice";
import * as loadingScreenSlice from "../features/loadingScreen/loadingScreenSlice";
import { RestWindowCommunication } from "./socketMiddlewareSlice";
import { createStore } from "./store";

export const rootReducer = combineReducers({
    websockets: websocketSlice.reducer,
    content: contentSlice.reducer,
    wizard: wizardSlice.reducer,
    app: appSlice.reducer,
    developer: developerSlice.reducer,
    jobs: jobsSlice.reducer,
    loadingScreen: loadingScreenSlice.reducer,
});

export const actions = {
    ...contentSlice.actions,
    ...wizardSlice.actions,
    ...appSlice.actions,
    ...websocketSlice.actions,
    ...developerSlice.actions,
    ...jobsSlice.actions,
    ...loadingScreenSlice.actions,
};

export const getState = (state: RootState): RootState => state;

const windowComms: WindowCommunication =
    window.parent.windowCommunication ?? new RestWindowCommunication();
export const store = createStore(
    rootReducer,
    [],
    window.__REDUX_DEVTOOLS_EXTENSION__?.(),
    windowComms,
);

export type ContentManagerStore = typeof store;

// Infer the `RootState` and `AppDispatch` types from the store itself
export type RootState = ReturnType<typeof store.getState>;
// Inferred type: {posts: PostsState, comments: CommentsState, users: UsersState}
export type AppDispatch = typeof store.dispatch;

export type MyDispatch = Dispatch<string>;
