// import reportWebVitals from "./reportWebVitals";
import "primeicons/primeicons.css";
import "primereact/resources/primereact.css";
import "primereact/resources/themes/nova/theme.css";
import React from "react";
import { Provider } from "react-redux";
import App from "./App";
import "./index.scss";
import "./primereact-override.scss";
import { actions, store } from "./redux/CMTStore";
import { createRoot } from "react-dom/client";

void ((): void => {
    if (process.env.NODE_ENV === "production") {
        //Dispatch websocket "connected" action.
        //For MCC environment, websocket connection gets initialized elsewhere.
        //We can assume it is connected.
        store.dispatch(actions.wsConnected());
    }

    /* root should always be present, also assertion is mentioned as the recommended way in the doc- https://reactjs.org/blog/2022/03/08/react-18-upgrade-guide.html
    so disabling the eslint warning */
    // eslint-disable-next-line  @typescript-eslint/no-non-null-assertion
    const root = createRoot(document.getElementById("root")!);

    root.render(
        <React.StrictMode>
            <Provider store={store}>
                <App />
            </Provider>
        </React.StrictMode>,
    );

    if (process.env.NODE_ENV === "development") {
        // If you want to start measuring performance in your app, pass a function
        // to log results (for example: reportWebVitals(console.log))
        // or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
        // reportWebVitals(console.log);
    }

    const version = process.env.REACT_APP_VERSION ?? ": Unknown Version";
    console.log(`Content Manager UI ${version}`);
})();
