import React from "react";
import "../scss/wizard.scss";
import "./scss/deviceNotSupported.scss";
import { deviceSupportSummary } from "./DeviceSupport";
import {
    MELODY_TECHNICAL_HELP_LINK,
    CLASSIC_TECHNICAL_HELP_LINK,
    HARMONY_TECHNICAL_HELP_LINK,
} from "../../common/common";

type ContentTypeProps = {
    name: string;
    description: string;
    link: string;
};

const ContentTypeRow: React.FC<ContentTypeProps> = (props): JSX.Element => {
    return (
        <tr>
            <td className="key-column">{props.name}</td>
            <td>{props.description}</td>
            <td>
                <span
                    className={"text-link"}
                    onClick={(): void => {
                        window.open(props.link);
                    }}
                >
                    Documentation
                </span>
            </td>
        </tr>
    );
};

export class DeviceNotSupported extends React.Component {
    render(): JSX.Element {
        return (
            <div>
                <p className={"centered"}>
                    The selected device is <b>not supported</b> by any of the MCC content
                    types.
                </p>
                <p className={"centered"}>
                    <b>How to fix this:</b> change your project device to one supported by
                    the MCC content types.
                </p>
                <p className={"centered"}>
                    Summary of available content types and supported device familes:
                </p>
                <table>
                    <thead>
                        <tr>
                            <th>Content type</th>
                            <th>Supported device families</th>
                            <th>Details</th>
                        </tr>
                    </thead>
                    <tbody>
                        <ContentTypeRow
                            name="MCC Melody"
                            description={deviceSupportSummary.MELODY}
                            link={MELODY_TECHNICAL_HELP_LINK}
                        />
                        <ContentTypeRow
                            name="MCC Classic"
                            description={deviceSupportSummary.CLASSIC}
                            link={CLASSIC_TECHNICAL_HELP_LINK}
                        />
                        <ContentTypeRow
                            name="MPLAB Harmony"
                            description={deviceSupportSummary.HARMONY}
                            link={HARMONY_TECHNICAL_HELP_LINK}
                        />
                    </tbody>
                </table>
            </div>
        );
    }
}
