import * as React from "react";
import type { ExpandedKeys } from "../../types";
import { Column } from "primereact/column";
import type {
    TreeTableSelectionKeys,
    TreeTableSelectionModeType,
    TreeTableSelectionParams,
} from "primereact/treetable";
import { TreeTable } from "primereact/treetable";
import { isMyTreeNode, MyTreeNode } from "../../../model/types/TreeNode";
import { getAllKeysAsExpanded } from "../../../model/types/TreeNode";
import { ContextMenu } from "primereact/contextmenu";
import styles from "../../ContentManager/components/ContentManagerTable/scss/content-manager.module.scss";
import { getOptionItems } from "./Options";
import JobProgress from "../../Jobs/JobProgress";

export type Props = {
    tableData: MyTreeNode[];
    expandKeys?: MyTreeNode[];
    filterData?: string[];
    selection?: TreeTableSelectionKeys;
    selectionMode?: TreeTableSelectionModeType;
    showProgress: boolean;
    setSelection?: (selection: TreeTableSelectionParams) => void;
    onSelect?: (node: MyTreeNode) => void;
    onUnselect?: (node: MyTreeNode) => void;
};

export const Table: React.FC<Props> = (props): JSX.Element => {
    const cm = React.useRef<ContextMenu>(null);
    const [expandedKeys, setExpandedKeys] = React.useState<ExpandedKeys>({});

    React.useEffect(() => {
        setExpandedKeys(getAllKeysAsExpanded(props?.expandKeys ?? []));
    }, [props.expandKeys]);

    return (
        <div>
            <ContextMenu
                ref={cm}
                model={getOptionItems({
                    onExpandAll: () =>
                        setExpandedKeys(getAllKeysAsExpanded(props.tableData)),
                    onCollapseAll: () => setExpandedKeys({}),
                })}
            />
            <TreeTable
                value={props.tableData}
                expandedKeys={expandedKeys}
                selectionMode={props.selectionMode}
                onToggle={(e): void => setExpandedKeys(e.value)}
                sortMode="multiple"
                resizableColumns={true}
                autoLayout={true}
                loading={props.tableData.length === 0}
                selectionKeys={props.selection}
                onContextMenu={(event): void => {
                    cm.current?.show(
                        event.originalEvent as unknown as React.SyntheticEvent,
                    );
                }}
                onSelectionChange={(e): void => {
                    props.setSelection?.(e);
                }}
                onUnselect={(e): void => {
                    if (isMyTreeNode(e.node)) {
                        //typeguard to ensure we're using correct type
                        props?.onUnselect?.(e.node);
                    }
                }}
                onSelect={(e): void => {
                    if (isMyTreeNode(e.node)) {
                        //typeguard to ensure we're using correct type
                        props?.onSelect?.(e.node);
                    }
                }}
            >
                <Column
                    bodyClassName={styles.tableBody}
                    field="moduleName"
                    header={"Component"}
                    sortable={true}
                    expander
                />

                <Column
                    bodyClassName={styles.tableBody}
                    header="Version"
                    field="moduleVersion"
                    body={(rowData: MyTreeNode): string | undefined =>
                        rowData.data?.moduleVersion
                    }
                />

                {props.showProgress && (
                    <Column
                        bodyClassName={styles.tableBody}
                        header="Update progress"
                        body={(rowData: MyTreeNode): JSX.Element => (
                            <JobProgress
                                name={rowData.data?.packageName}
                                compact={false}
                            />
                        )}
                    />
                )}

                <Column
                    className={styles.description}
                    headerClassName={`${styles.tableHeader} ${styles.description}`}
                    bodyClassName={styles.tableBody}
                    field="moduleDescription"
                    header="Description"
                />
            </TreeTable>
        </div>
    );
};
