import React from "react";
import type { ContentIdBundle, DisplayMode } from "./features/types";
import { EMPTY_CONTENT } from "./features/types";
import { RemoteContentManager } from "./model/RemoteContentManager/RemoteContentManager";
import { ShutdownNotice } from "./features/common/ShutdownNotice";
import * as Hooks from "./redux/hooks";
import isEqual from "lodash.isequal";
import LoadingScreen from "./features/loadingScreen/LoadingScreen";
import ContentManager from "./features/ContentManager/ContentManager";
import DeveloperToolbar from "./features/developerToolbar/DeveloperToolbar";
import Wizard from "./features/wizard/Wizard";
import Toast from "./features/toast/Toast";
import { ServerConfirmation } from "./features/common/ServerConfirmation";
import { values } from "./util/objects";

const ENABLE_DEV_TOOLBAR = process.env.NODE_ENV === "development";

const App: React.FC = (): JSX.Element => {
    const state = Hooks.useAppSelector((state) => state);

    const {
        requestCompatibilityErrors,
        requestContext,
        requestDownload,
        requestSetAvailableContent,
        setApply,
        setSelectedVersions,
    } = Hooks.useActions();

    const model = React.useMemo(
        () =>
            new RemoteContentManager({
                data: state.content.data,
                availableData: state.content.availableVersions,
                selectedVersions: state.content.selectedVersions,
                displayMode: state.app.displayMode,
                usedVersions: state.content.usedContent,
                categorySort: state.content.categorySort,
                shouldIncludePreReleaseVersions:
                    state.content.shouldIncludePreReleaseVersions,
            }),
        [
            state.app.displayMode,
            state.content.availableVersions,
            state.content.categorySort,
            state.content.data,
            state.content.selectedVersions,
            state.content.shouldIncludePreReleaseVersions,
            state.content.usedContent,
        ],
    );

    const [cachedAvailableSelectedVersions, setCachedAvailableSelectedVersions] =
        React.useState<ContentIdBundle>(EMPTY_CONTENT);
    const [hasCompatibilityErrors, setHasCompatibilityErrors] = React.useState(false);

    const availableSelectedVersions = model.getAvailableSelectedVersions();
    if (!isEqual(availableSelectedVersions, cachedAvailableSelectedVersions)) {
        setCachedAvailableSelectedVersions(availableSelectedVersions);
    }

    React.useEffect(() => {
        if (!isEqual(cachedAvailableSelectedVersions, EMPTY_CONTENT)) {
            requestCompatibilityErrors(cachedAvailableSelectedVersions);
        }
    }, [requestCompatibilityErrors, cachedAvailableSelectedVersions]);

    React.useEffect(() => {
        if (state.websockets.connectionStatus === "connected") {
            requestContext();
        }
    }, [requestContext, state.websockets.connectionStatus]);

    React.useEffect(() => {
        if (state.content.isApplying && state.content.isDownloading !== "loading") {
            setApply(false);
            requestSetAvailableContent(model.getAvailableSelectedVersions());
        }
    }, [
        setApply,
        requestSetAvailableContent,
        state.content.isApplying,
        state.content.isDownloading,
        model,
    ]);

    React.useEffect(() => {
        setHasCompatibilityErrors(
            values(cachedAvailableSelectedVersions).filter(
                (content) => content.length > 0,
            ).length > 0,
        );
    }, [cachedAvailableSelectedVersions]);

    React.useEffect(() => {
        if (hasCompatibilityErrors) {
            requestCompatibilityErrors(cachedAvailableSelectedVersions);
        }
    }, [hasCompatibilityErrors, cachedAvailableSelectedVersions]);

    function getDisplayComponent(mode: DisplayMode): JSX.Element {
        switch (mode) {
            case "WIZARD": {
                return (
                    <Wizard
                        model={model}
                        onFinished={(): void => {
                            setApply(true);
                            requestDownload(model.getContentForDownload());
                        }}
                        onOptionalSelect={setSelectedVersions}
                        catalogData={state.content.catalogData}
                        inProgress={
                            state.content.isDownloading === "loading" ||
                            state.content.isFetching === "loading"
                        }
                    />
                );
            }
            case "TABLE": {
                return <ContentManager model={model} />;
            }
            default: {
                return <LoadingScreen />;
            }
        }
    }

    return (
        <>
            <Toast
                localContentStatus={state.wizard.localContentStatus}
                downloadStatus={state.content.isDownloading}
                fetchStatus={state.content.isFetching}
            />
            {ENABLE_DEV_TOOLBAR && (
                <DeveloperToolbar isFetching={state.content.isFetching === "loading"} />
            )}
            {getDisplayComponent(state.app.displayMode)}
            {state.app.shutdownNotice && <ShutdownNotice {...state.app.shutdownNotice} />}
            {state.app.serverConfirmation && (
                <ServerConfirmation {...state.app.serverConfirmation} />
            )}
        </>
    );
};

export default App;
