import React from "react";
import { ProgressSpinner } from "primereact/progressspinner";
import { Status } from "../../types";

interface Props {
    updateStatus: Status;
    isLoading: boolean;
}

const CheckMark = (props: Props): JSX.Element => {
    const { isLoading, updateStatus } = props;
    const displayCheckMark =
        !isLoading && (updateStatus === "succeeded" || updateStatus === "idle");

    return (
        <div className="d-block p-1 me-2">
            {isLoading && (
                <ProgressSpinner
                    style={{ width: "20px", height: "20px", marginTop: "40%" }}
                />
            )}
            {displayCheckMark && (
                <span className="pi pi-check bg-primary rounded-circle text-white check-mark" />
            )}
            {!isLoading && updateStatus === "failed" && (
                <span className="pi pi-times bg-danger rounded-circle text-white check-mark" />
            )}
        </div>
    );
};

export default CheckMark;
