import { Column } from "primereact/column";
import { ContextMenu } from "primereact/contextmenu";
import type { TreeTableSelectionKeys } from "primereact/treetable";
import { TreeTable } from "primereact/treetable";
import * as React from "react";
import type { ContentId } from "../../../../model/types/ContentId";
import type { VersionStatus } from "../../../../model/types/RowData";
import type { MyTreeNode } from "../../../../model/types/TreeNode";
import { getFilteredTreeTableData } from "../../../../model/utils/searchTermFilter/FilterTreeTableModel";
import type { SelectedNodes } from "../../../../model/utils/selectionUtil";
import type { ExpandedKeys } from "../../../types";
import { ModuleColumnBody } from "./components/moduleColumn/Body";
import { getOptionItems } from "./components/options/Options";
import { StatusColumn } from "./components/statusColumn/StatusColumn";
import { ProgressColumn } from "./components/progressColumn/ProgressColumn";
import { Version } from "./components/versionColumn/Body";
import styles from "./scss/content-manager.module.scss";
import "./scss/primereact-override.scss";
import { getAllKeysAsExpanded, hasParent } from "../../../../model/types/TreeNode";
import { MultiSelect } from "primereact/multiselect";
import { actions } from "../../contentManagerSlice";
import * as Hooks from "../../../../redux/hooks";
import { buildTree, getLocalContentOnly, getUsedContentOnly } from "../tableFilters";

export interface ContentManagerTableProps {
    tableData: MyTreeNode[];
    expandedKeys?: ExpandedKeys;
    selection: SelectedNodes;
    setSelection?: (value: SelectedNodes) => void;
    searchTerms?: string[];
    setSearchTerms?: (values: string[]) => void;
    onSelected?: (id: ContentId) => void;
    onDownload?: (id: ContentId[]) => void;
    onRefresh?: () => void;
    onRefreshFor?: (contentIds: ContentId[]) => void;
    onSelectLatest?: () => void;
    getVersionStatus?: (id: string, version: string) => VersionStatus | undefined;
    defaultsUnselected?: string[];
    setDefaultsUnselected?: (keys: string[]) => void;
}

enum FilterType {
    LOCAL = "Local Content Only",
    DEVICE_SUPPORTED = "Device Supported Content Only",
    USED_CONTENT = "Used Content Only",
}

const DEFAULT_FILTER_TYPE =
    window.parent.windowCommunication !== undefined ? [FilterType.DEVICE_SUPPORTED] : [];

const ContentManagerTable: React.FC<ContentManagerTableProps> = (props): JSX.Element => {
    const [selectedNodeKey, setSelectedNodeKey] = React.useState<string | null>(null);
    const [filteredTableData, setFilteredTableData] = React.useState<MyTreeNode[]>(
        getFilteredTreeTableData(props.tableData ?? [], props.searchTerms ?? []),
    );
    const [filters, setFilters] = React.useState<FilterType[]>(DEFAULT_FILTER_TYPE);
    const [filteredContent, setFilteredContent] = React.useState<MyTreeNode[]>([]);
    const [expandedKeys, setExpandedKeys] = React.useState<ExpandedKeys>(
        props.expandedKeys ?? {},
    );
    const state = Hooks.useAppSelector((state) => state);
    const dispatch = Hooks.useAppDispatch();

    const cm = React.useRef<ContextMenu>(null);

    React.useEffect(() => {
        const filteredTable = getFilteredTreeTableData(
            props.tableData ?? [],
            props.searchTerms ?? [],
        );
        setFilteredTableData(filteredTable);

        if (props.searchTerms && props.searchTerms.length > 0) {
            setExpandedKeys(getAllKeysAsExpanded(filteredTable));
            setFilters(DEFAULT_FILTER_TYPE);
        }
    }, [props.searchTerms, props.tableData]);

    React.useEffect(() => {
        let filtered: MyTreeNode[] = [...filteredTableData];
        if (filters.length === 0) {
            setFilteredContent([]);
            dispatch(actions.setViewMode("all"));
            return;
        }
        if (filters.includes(FilterType.DEVICE_SUPPORTED)) {
            dispatch(actions.setViewMode("available"));
        } else {
            dispatch(actions.setViewMode("all"));
        }
        if (filters.includes(FilterType.LOCAL)) {
            filtered = buildTree(getLocalContentOnly(filtered), filtered);
        }
        if (filters.includes(FilterType.USED_CONTENT)) {
            filtered = buildTree(
                getUsedContentOnly(filtered, state.content.usedContent),
                filtered,
            );
        }

        setFilteredContent(filtered);
    }, [filters, filteredTableData, state.content.usedContent]);

    const getFilterSelect = (): JSX.Element => {
        return (
            <span>
                <MultiSelect
                    className={"filter-options"}
                    placeholder={"Filter Content"}
                    options={[
                        {
                            label: FilterType.DEVICE_SUPPORTED,
                            value: FilterType.DEVICE_SUPPORTED,
                        },
                        {
                            label: FilterType.LOCAL,
                            value: FilterType.LOCAL,
                        },
                        {
                            label: FilterType.USED_CONTENT,
                            value: FilterType.USED_CONTENT,
                        },
                    ]}
                    value={filters}
                    onChange={(e): void => {
                        setFilters(e.target.value);
                    }}
                />
            </span>
        );
    };

    const isExpanded = (key: string | number | undefined): boolean => {
        return key !== undefined && Object.keys(expandedKeys).includes(key.toString());
    };

    return (
        <>
            <ContextMenu
                ref={cm}
                model={getOptionItems({
                    currentSelection: selectedNodeKey ?? "",
                    onSelectLatest: () => {
                        props.onSelectLatest?.();
                    },
                    onExpandAll: () =>
                        setExpandedKeys(getAllKeysAsExpanded(filteredTableData)),
                    onCollapseAll: () => setExpandedKeys({}),
                    onRefresh: () => dispatch(actions.requestRefresh()),
                })}
                onHide={(): void => setSelectedNodeKey(null)}
            />
            <div>
                <TreeTable
                    value={
                        filteredContent.length > 0 ? filteredContent : filteredTableData
                    }
                    expandedKeys={expandedKeys}
                    onToggle={(e): void => {
                        setExpandedKeys(e.value);
                    }}
                    className="content-manager-table"
                    sortMode="multiple"
                    resizableColumns={true}
                    autoLayout={true}
                    selectionKeys={props.selection as unknown as TreeTableSelectionKeys}
                    onSelectionChange={(e): void => {
                        props.setSelection?.(e.value as unknown as SelectedNodes);
                    }}
                    onUnselect={(e): void => {
                        const node = e.node;
                        if (node.key && props.defaultsUnselected) {
                            props.setDefaultsUnselected?.([
                                ...props.defaultsUnselected,
                                node.key.toString(),
                            ]);
                        }
                    }}
                    onSelect={(e): void => {
                        const node = e.node;
                        if (node.key && props.defaultsUnselected) {
                            props.setDefaultsUnselected?.(
                                props.defaultsUnselected.filter(
                                    (key) => key !== node.key,
                                ),
                            );
                        }
                    }}
                    contextMenuSelectionKey={selectedNodeKey ?? ""}
                    onContextMenuSelectionChange={(event): void =>
                        setSelectedNodeKey?.(event.value as unknown as string)
                    }
                    onContextMenu={(event): void => {
                        cm.current?.show(
                            event.originalEvent as unknown as React.SyntheticEvent,
                        );
                    }}
                    loading={props.tableData.length === 0}
                >
                    <Column
                        headerClassName={`${styles.tableHeader} ${styles.moduleName}`}
                        bodyClassName={styles.tableBody}
                        field="moduleName"
                        header={
                            <span>
                                Component
                                {props.searchTerms?.length === 0 && getFilterSelect()}
                            </span>
                        }
                        body={(rowData: MyTreeNode): JSX.Element => {
                            const key = rowData.key;
                            if (
                                isExpanded(key) ||
                                (hasParent(rowData) && rowData.children.length === 0)
                            ) {
                                return (
                                    <ModuleColumnBody
                                        isExpanded={true}
                                        rowData={rowData}
                                    />
                                );
                            }
                            return <ModuleColumnBody rowData={rowData} />;
                        }}
                        sortable={true}
                        expander
                    />

                    <Column
                        headerClassName={`${styles.tableHeader} ${styles.version}`}
                        bodyClassName={styles.tableBody}
                        header="Version"
                        field="moduleVersion"
                        body={(rowData: MyTreeNode): JSX.Element => (
                            <Version
                                rowData={rowData}
                                selectedData={rowData}
                                onDownload={(contentIds: ContentId[]): void => {
                                    props.onDownload?.(contentIds);
                                }}
                                isCalculatingCompatability={
                                    state.content.isCalculatingCompatability
                                }
                                setSelected={props.onSelected}
                                getVersionStatus={props.getVersionStatus}
                                onRefreshFor={props.onRefreshFor}
                            />
                        )}
                    />

                    <Column
                        headerClassName={`${styles.tableHeader} ${styles.status}`}
                        bodyClassName={styles.tableBody}
                        header="Status"
                        field="status"
                        style={{ textAlign: "center" }}
                        body={(rowData: MyTreeNode): JSX.Element => (
                            <StatusColumn
                                node={rowData}
                                onClick={(): void | undefined =>
                                    props.setSearchTerms?.(["update"])
                                }
                            />
                        )}
                    />

                    <Column
                        headerClassName={`${styles.tableHeader} ${styles.progress}`}
                        bodyClassName={styles.tableBody}
                        header="Update progress"
                        field="progress"
                        body={(rowData: MyTreeNode): JSX.Element => (
                            <ProgressColumn node={rowData} />
                        )}
                    />

                    <Column
                        className={styles.description}
                        headerClassName={`${styles.tableHeader} ${styles.description}`}
                        bodyClassName={styles.tableBody}
                        field="moduleDescription"
                        header="Description"
                    />
                </TreeTable>
            </div>
        </>
    );
};
export default ContentManagerTable;
