/* eslint-disable @typescript-eslint/no-magic-numbers */
import { Button } from "primereact/button";
import { Dropdown } from "primereact/dropdown";
import type { DropdownChangeParams } from "primereact/dropdown";
import React from "react";
import type { ContentId } from "../../../../../../model/types/ContentId";
import type { RowData, VersionStatus } from "../../../../../../model/types/RowData";
import type { MyTreeNode } from "../../../../../../model/types/TreeNode";
import { getKeys } from "../../../../../../model/types/Utils";
import { sort } from "../../../../../../model/utils/semver";
import * as Hooks from "../../../../../../redux/hooks";
import { flatValues, values } from "../../../../../../util/objects";
import { getState } from "../../../../contentManagerSlice";
import "./scss/version.scss";
import { Tooltip } from "primereact/tooltip";

interface VersionProps {
    rowData: MyTreeNode;
    selectedData?: MyTreeNode;
    currentSelection?: string;
    isCalculatingCompatability: boolean;
    onDownload?: (id: ContentId[]) => void;
    setSelected?: (contentIds: ContentId) => void;
    getVersionStatus?: (id: string, version: string) => VersionStatus | undefined;
    onRefresh?: () => void;
    onRefreshFor?: (contentIds: ContentId[]) => void;
}

export const Version: React.FC<VersionProps> = (
    props: VersionProps & { children?: React.ReactNode },
): JSX.Element => {
    const { data } = props.rowData;
    const state = getState(Hooks.useAppSelector((state) => state));

    const getReadmeUrl = (option: string): string | undefined => {
        if (data?.versionReadMeUrlMap !== undefined) {
            if (data?.versionReadMeUrlMap[option] !== undefined) {
                return data?.versionReadMeUrlMap[option];
            }
        }
    };

    const getOptionTemplate = (
        option: string,
        data: RowData,
        status?: VersionStatus,
        errors?: string[],
    ): JSX.Element => {
        const stateNode = values(state.data)
            .flat()
            .filter((node) => node.meta?.category === data.meta?.category)
            .find((node) => node.name === data.id);

        const getVersionCheckmark = (
            selectedVersion: string | undefined,
            mccVersion: string,
        ): JSX.Element => {
            const style = selectedVersion === mccVersion ? "version-selected" : "version";
            return <i className={`pi pi-check ${style}`} />;
        };

        const isIncompatible: boolean = (errors?.length ?? 0) > 0;
        const rowStyle = isIncompatible ? "incompatible-version-row" : "";
        const labelStyle = isIncompatible ? "incompatible-version" : "";
        const readmeUrl = getReadmeUrl(option);

        const ErrorTooltip = (target: string): JSX.Element => {
            return (
                <ul className={"error-tooltip"}>
                    {errors?.map((error, index) => (
                        <Tooltip
                            target={`.incompatible-version-row-${target}`}
                            position="right"
                            className="dropdown-tooltip"
                            key={`${error}-${index}`}
                        >
                            <li>{error}</li>
                        </Tooltip>
                    ))}
                </ul>
            );
        };

        const VersionIcon = (): JSX.Element | null => {
            if (option === stateNode?.version) {
                return getVersionCheckmark(
                    props.selectedData?.data?.moduleVersion,
                    stateNode?.version,
                );
            } else if (isIncompatible) {
                return <i className={"pi pi-ban incompatible-version-icon"} />;
            }
            return null;
        };

        const formattedOptionclass = option.replaceAll(".", "");

        return (
            <div
                className={`row ${rowStyle}-${formattedOptionclass}`}
                data-pr-position="right"
            >
                <div className={"col-2"}>
                    <span>
                        <VersionIcon />
                        {isIncompatible && ErrorTooltip(formattedOptionclass)}
                    </span>
                </div>
                <div className={"col-7"}>
                    <span className={labelStyle}>
                        {option + ` - [${status?.toLowerCase() ?? "Unknown"}]`}
                    </span>
                </div>
                <div className={"col-3 version-link-column"}>
                    {readmeUrl !== undefined && (
                        <Button
                            className={`p-button-rounded p-button-text version-info-button`}
                            icon="pi pi-external-link"
                            tooltip="Click to Open Version History"
                            tooltipOptions={{ position: "bottom" }}
                            onClick={(
                                event: React.MouseEvent<HTMLButtonElement>,
                            ): void => {
                                event.stopPropagation();
                                window.open(readmeUrl, "_blank");
                            }}
                        />
                    )}
                </div>
            </div>
        );
    };

    function shouldDisplayVersion(): boolean {
        const hasLocalContent = values(data?.versions ?? {}).includes("LOCAL");
        const isUserSelected =
            state.selectedVersionsDiff.find((v) => v.name === data?.id) !== undefined;
        const isAvailable =
            flatValues(state.availableVersions).find((v) => v.name === data?.id) !==
            undefined;
        return (
            isUserSelected ||
            hasLocalContent ||
            (data?.meta?.defaultSelection !== undefined && isAvailable)
        );
    }

    function hasCompatibilityErrors(version?: string): boolean {
        if (version == null) {
            return false;
        }
        return (data?.compatibilityErrors?.[version]?.length ?? 0) > 0;
    }
    function onVersionChange(e: DropdownChangeParams): void {
        const version = toStringOrUndefined(e.target.value);

        if (!hasCompatibilityErrors(version)) {
            props.setSelected?.({
                name: props.rowData.data?.id ?? "",
                version,
            });
        }
    }

    return (
        <>
            {data && data.moduleVersion && (
                <>
                    <div>
                        <Dropdown
                            className="version-dropdown"
                            value={
                                shouldDisplayVersion() ? data?.moduleVersion : undefined
                            }
                            disabled={props.isCalculatingCompatability}
                            placeholder=""
                            options={sort(
                                getKeys(data.versions ?? {}),
                                state.shouldIncludePreReleaseVersions,
                            )}
                            tooltip={"Active Version"}
                            appendTo={document.getElementById("portal") ?? undefined}
                            onChange={onVersionChange}
                            itemTemplate={(option: string): JSX.Element =>
                                getOptionTemplate(
                                    option,
                                    data,
                                    props.getVersionStatus?.(data.id ?? "", option),
                                    data?.compatibilityErrors?.[option],
                                )
                            }
                        />
                    </div>
                </>
            )}
        </>
    );
};

const toStringOrUndefined = (x: unknown): string | undefined =>
    typeof x === "string" ? x : undefined;
