import type { Content } from "../BaseContent/Content";
import { ContentManager } from "../BaseContent/ContentModel";
import type { TridionManifest } from "./TridionManifest";
import { TridionContent } from "./TridionContent";

export class TridionManager extends ContentManager<TridionManifest, TridionContent> {
    protected getNewContent = (
        name: string,
        data: TridionManifest,
        includePreReleaseVersions: boolean,
    ): Content<TridionManifest> => {
        return new TridionContent(name, data, includePreReleaseVersions);
    };
}
