import { Toast as PrimeReactToast } from "primereact/toast";
import React from "react";
import type { LocalContentStatus, Status } from "../../features/types";

const processDownloadToast = (
    toast: React.RefObject<PrimeReactToast>,
    downloadStatus: Status,
): void => {
    switch (downloadStatus) {
        case "loading":
            toast.current?.show({
                severity: "info",
                summary: "Downloading",
                detail: "Download In Progress",
                life: 3000,
            });
            break;
        case "succeeded":
            toast.current?.show({
                severity: "success",
                summary: "Download",
                detail: "Download Complete",
                life: 3000,
            });
            break;
        case "failed":
            toast.current?.show({
                severity: "error",
                summary: "Download",
                detail: "Download Failed",
                life: 3000,
            });
            break;
        default:
            break;
    }
};

const processFetchToast = (
    toast: React.RefObject<PrimeReactToast>,
    fetchStatus: Status,
): void => {
    switch (fetchStatus) {
        case "loading":
            toast.current?.show({
                severity: "info",
                summary: "Refreshing",
                detail: "Refresh In Progress",
                life: 3000,
            });
            break;
        case "succeeded":
            toast.current?.show({
                severity: "success",
                summary: "Refresh",
                detail: "Refresh Complete",
                life: 3000,
            });
            break;
        case "failed":
            toast.current?.show({
                severity: "error",
                summary: "Refresh",
                detail: "Refresh Failed",
                life: 3000,
            });
            break;
        default:
            break;
    }
};

type Props = {
    localContentStatus: LocalContentStatus;
    downloadStatus: Status;
    fetchStatus: Status;
};

const Toast: React.FC<Props> = (props) => {
    const ref = React.useRef<PrimeReactToast>(null);

    React.useEffect(() => {
        if (props.localContentStatus !== "ALL_CONTENT_DOWNLOADED") {
            processDownloadToast(ref, props.downloadStatus);
        }
    }, [props.downloadStatus, props.localContentStatus]);

    React.useEffect(() => {
        processFetchToast(ref, props.fetchStatus);
    }, [props.fetchStatus]);

    return <PrimeReactToast ref={ref} position="bottom-right" />;
};

export default Toast;
