import * as React from "react";
import type { RemoteContentManager } from "../../model/RemoteContentManager/RemoteContentManager";
import type { SelectedNodes } from "../../model/utils/selectionUtil";
import * as Hooks from "../../redux/hooks";
import ContentManagerTable from "./components/ContentManagerTable/ContentManagerTable";
import Header from "./components/Header/Header";
import { getState } from "./contentManagerSlice";
import "./scss/primereact-override.scss";

export interface ContentManagerProps {
    model: RemoteContentManager;
}

const ContentManager: React.FC<ContentManagerProps> = ({ model }): JSX.Element => {
    const {
        requestDownload,
        requestRefresh,
        requestRefreshFor,
        requestSetAvailableContent,
        setApply,
        setSearchTerms,
        setSelectedVersion,
        setSelectedVersions,
    } = Hooks.useActions();

    const state = Hooks.useAppSelector(getState);
    const [selection, setSelection] = React.useState<SelectedNodes>({});

    const selectLatest = (): void => {
        setSelectedVersions(
            model.getLatestVersions(model.getModulesWithAvailableUpdates()),
        );
    };

    const treeTableModel =
        state.viewMode === "available"
            ? model.getAvailableData()
            : model.getProcessedData();

    React.useEffect(() => {
        if (state.isApplying && state.isDownloading !== "loading") {
            setApply(false);
            requestSetAvailableContent(model.getAvailableSelectedVersions());
        }
    }, [
        model,
        state.isApplying,
        state.isDownloading,
        setApply,
        requestSetAvailableContent,
    ]);

    return (
        <>
            <Header
                globalFilter={state.searchTerms}
                setGlobalFilter={(searchTerms): void => {
                    setSearchTerms(searchTerms);
                }}
                inProgress={
                    state.isDownloading === "loading" || state.isFetching === "loading"
                }
                onApply={(): void => {
                    setApply(true);
                    requestDownload(model.getContentForDownload());
                }}
                onCancel={(): void => {
                    setApply(false);
                }}
                onSelectLatest={(): void => {
                    selectLatest();
                }}
            />
            <div>
                <ContentManagerTable
                    selection={selection}
                    setSelection={setSelection}
                    tableData={treeTableModel}
                    searchTerms={state.searchTerms}
                    setSearchTerms={setSearchTerms}
                    onDownload={(ids): void => {
                        requestDownload(model.getContentIdBundleForDownload(ids));
                    }}
                    onRefresh={requestRefresh}
                    onRefreshFor={requestRefreshFor}
                    onSelectLatest={selectLatest}
                    onSelected={setSelectedVersion}
                    getVersionStatus={model.getVersionStatus}
                />
            </div>
        </>
    );
};
export default ContentManager;
