import type { Dispatch, Middleware, Reducer, Store, StoreEnhancer } from "redux";
import * as Redux from "redux";
import { createRequestMiddleware } from "./request-middleware";

type Render = (action: string) => void;
interface Bridge {
    request: Dispatch;
    setResponseHandler: (render: Render) => void;
    refresh: () => void;
}

const toString = (x: unknown): string => (typeof x === "string" ? x : JSON.stringify(x));

const create = <S, A extends Redux.AnyAction>(
    { request, setResponseHandler, refresh }: Bridge,
    reducer: Reducer<S, A>,
    otherMiddleware: Middleware[],
    otherEnhancer?: StoreEnhancer,
): Store<S, A> => {
    const middlewareEnhancer: StoreEnhancer = Redux.applyMiddleware(
        createRequestMiddleware(request, refresh),
        ...otherMiddleware,
    );

    const enhancers = otherEnhancer
        ? Redux.compose(middlewareEnhancer, otherEnhancer)
        : middlewareEnhancer;

    const store: Store<S, A> = Redux.createStore(
        reducer,
        /* initialState */
        enhancers,
    );

    setResponseHandler((data: string): void => {
        const action: A = JSON.parse(data) as unknown as A;
        console.log("Response: " + toString(action.type));
        store.dispatch(action);
    });
    return store;
};

export const STUB_WINDOW_COMMUNICATION: WindowCommunication = {
    send: <T>(name: string, args: T[]) => {
        /* Do nothing */
    },
    registerFunction: <T>(name: string, fn: (...args: T[]) => void) => {
        /* Do nothing */
    },
    deregisterFunction: () => {
        /* Do nothing */
    },
};

export const createStore = <S, A extends Redux.AnyAction>(
    reducer: Reducer<S, A>,
    otherMiddleware: Middleware[],
    otherEnhancer?: StoreEnhancer,
    window: WindowCommunication = STUB_WINDOW_COMMUNICATION,
): Store<S, A> =>
    create(
        {
            request: (action) => {
                window.send("request", [action]);
                return action;
            },
            setResponseHandler: (fn) => {
                window.registerFunction("response", fn);
            },
            refresh: () => {
                window.send("refresh", []);
            },
        },
        reducer,
        otherMiddleware,
        otherEnhancer,
    );
