import React from "react";
import * as Hooks from "../../../redux/hooks";
import { actions, getState } from "../../ContentManager/contentManagerSlice";
import { sort } from "../../../model/utils/semver";
import { Button } from "primereact/button";
import CheckMark from "./CheckMark";
import UpdateStatus from "./UpdateSatus";

const MccFrontendUpgrade = (): JSX.Element => {
    const dispatch = Hooks.useAppDispatch();
    const state = getState(Hooks.useAppSelector((state) => state));
    const frontendLatestVersion = state.frontendLatestVersion;
    const installedFrontendVersion = state.installedFrontendVersion;
    const sortedFrontendVersions =
        installedFrontendVersion && frontendLatestVersion
            ? sort([installedFrontendVersion, frontendLatestVersion], true)
            : [];
    const isFrontEndUpdateAvailable =
        sortedFrontendVersions[0] &&
        sortedFrontendVersions[0] !== installedFrontendVersion;
    const frontendUpdateStatus = state.frontendUpdateStatus;
    const isLoading = frontendUpdateStatus === "loading";

    React.useEffect(() => {
        if (frontendLatestVersion === undefined) {
            dispatch(actions.requestMCCFrontendUpdate());
        }
    }, [frontendLatestVersion, dispatch]);

    React.useEffect(() => {
        if (frontendUpdateStatus === "succeeded") {
            dispatch(actions.setInstalledMCCFrontendVersion(frontendLatestVersion ?? ""));
        }
    }, [frontendUpdateStatus, frontendLatestVersion, dispatch]);

    const updateFrontend = (): void => {
        if (frontendLatestVersion) {
            dispatch(actions.requestMCCFrontendDownload(frontendLatestVersion));
        }
    };

    return (
        <>
            {state.showMCCFrontendUpdate && (
                <div className="row border pb-2 mt-2">
                    <div className="col mt-2">
                        <span>
                            <b>MCC Frontend</b>
                        </span>
                    </div>
                    <div className="col-12 d-flex">
                        <CheckMark
                            updateStatus={frontendUpdateStatus}
                            isLoading={isLoading}
                        />
                        <UpdateStatus
                            libraryName="MCC Frontend"
                            updateStatus={frontendUpdateStatus}
                            currentVersion={installedFrontendVersion ?? ""}
                            latestVersion={frontendLatestVersion ?? ""}
                            isUpToDate={!isFrontEndUpdateAvailable}
                        />
                    </div>
                    {isFrontEndUpdateAvailable && (
                        <div className="col-12">
                            <Button
                                className="mt-2 me-2"
                                label="Download and Install"
                                icon="pi pi-cloud-download"
                                loading={isLoading}
                                onClick={updateFrontend}
                            />
                        </div>
                    )}
                </div>
            )}
        </>
    );
};

export default MccFrontendUpgrade;
