// https://mostly-adequate.gitbooks.io/mostly-adequate-guide/content/appendix_c.html

export const prop =
    <M, K extends keyof M = keyof M>(k: K) =>
    (m: M): M[K] =>
        m[k];

export const match =
    <M, K extends keyof M = keyof M>(map: (m: M) => M[K] | undefined) =>
    (v: M[K]) =>
    (m: M): boolean =>
        map(m) === v;

export const values = <V>(r: Record<string | number | symbol, V> = {}): V[] =>
    map(r, (k, v) => v);

export const flatValues = <V>(r: Record<string | number | symbol, V[]> = {}): V[] =>
    flatMap(r, (k, v) => v);

/** Flat Map: Record<string, V[]> to B[] */
export const flatMap = <V, B>(
    r: Record<string, V[]> = {},
    fn: (k: string, v: V[]) => B[],
): B[] => {
    const b: B[] = [];
    forEach(r, (k, v) => b.push(...fn(k, v)));
    return b;
};

/** Map: Record<string, V> to B[] */
export const map = <V, B>(r: Record<string, V> = {}, fn: (k: string, v: V) => B): B[] => {
    const b: B[] = [];
    forEach(r, (k, v) => b.push(fn(k, v)));
    return b;
};

export const findFirst = <V>(
    r: Record<string, V> = {},
    fn: (k: string, v: V) => boolean,
): string | undefined => {
    for (const k in r) {
        if (fn(k, r[k])) return k;
    }
    return undefined;
};

/** For Each: Record<string, V> to void */
export const forEach = <V>(
    r: Record<string, V> = {},
    fn: (k: string, v: V) => void,
): void => {
    for (const k in r) {
        fn(k, r[k]);
    }
};

/** Filter the record according to the given function. */
export const filter = <V>(
    r: Record<string, V> = {},
    fn: (k: string, v: V) => boolean,
): Record<string, V> =>
    Object.keys(r)
        .filter((k) => fn(k, r[k]))
        .reduce((obj, k) => ({ ...obj, [k]: r[k] }), {});
