import * as React from "react";
import { Accordion, AccordionTab } from "primereact/accordion";
import type { TreeTableSelectionKeys } from "primereact/treetable";
import type { Meta } from "../../model/BaseContent/Manifest";
import type { RemoteContentManager } from "../../model/RemoteContentManager/RemoteContentManager";
import type { ContentId } from "../../model/types/ContentId";
import type { MyTreeNode } from "../../model/types/TreeNode";
import { getLeaves } from "../../model/types/TreeNode";
import * as Hooks from "../../redux/hooks";
import { CONTENT_MANAGER_TECHNICAL_HELP_LINK } from "../common/common";
import { HelpIcon } from "../common/HelpIcon";
import { Settings } from "../settings/Settings";
import { contentSlice } from "../ContentManager/contentManagerSlice";
import type {
    ContentBundle,
    ContentIdBundle,
    LocalContentStatus,
    WizardSteps,
} from "../types";
import { ContentSelector } from "./components/ContentSelector";
import Nav from "./components/Nav";
import { DeviceNotSupported } from "./components/DeviceNotSupported";
import { actions, getState } from "./wizardSlice";
import { Table } from "./components/Table";
import "./scss/wizard.scss";
import { ProgressSpinner } from "primereact/progressspinner";

export type Props = {
    model: RemoteContentManager;
    catalogData: ContentBundle<Meta[], Meta[], Meta[], Meta[]>;
    inProgress?: boolean;
    onFinished: () => void;
    onOptionalSelect: (ids: ContentId[]) => void;
};

const Wizard: React.FC<Props> = (props): JSX.Element => {
    const [showSettings, setShowSettings] = React.useState(false);
    const [selection, setSelection] = React.useState<TreeTableSelectionKeys>();
    const dispatch = Hooks.useAppDispatch();
    const state = Hooks.useAppSelector(getState);

    const requiredContent: ContentId[] = props.model.getRequiredContent();
    const missingRequiredContent: MyTreeNode[] =
        props.model.getMissingRequiredContentTreeData(state.contentType);
    const optionalContent: MyTreeNode[] = props.model.getOptionalRemoteData(
        state.contentType,
    );
    const selectedVersions: ContentIdBundle = props.model.getAvailableSelectedVersions();

    let contentStatus: LocalContentStatus = "NO_CONTENT_DOWNLOADED";

    React.useEffect(() => {
        if (state.supportedContentTypes.length === 0) {
            dispatch(actions.setWizardStep("HELP"));
        }
    }, [state.supportedContentTypes]);

    React.useEffect(() => {
        if (state.contentType) {
            dispatch(contentSlice.actions.requestInitialContent(state.contentType));
        }
    }, [state.contentType]);

    React.useEffect(() => {
        dispatch(actions.setLocalContentStatus(contentStatus));
    }, [state.localContentStatus, contentStatus]);

    const getRequiredTableHeader = (): JSX.Element => {
        const AllRequiredContentLocallyHeader = (
            <p>
                All required content is available locally on your machine. No other
                download is needed to get started.
                <br />
                To change content versions later, access the Content Manager from Device
                Resources.
            </p>
        );
        const SomeRequiredContentLocallyHeader = (
            <p>
                {props.inProgress ? (
                    downloadStatusReqContentHeader()
                ) : (
                    <div>
                        Some required content must be downloaded. The following content
                        will be downloaded when you click on "Finish".
                    </div>
                )}
                To change content versions later, access the Content Manager from Device
                Resources.
            </p>
        );
        const NoRequiredContentLocallyHeader = (
            <p>
                {props.inProgress ? (
                    downloadStatusReqContentHeader()
                ) : (
                    <div>
                        The required content is not available locally. The following
                        content will be downloaded when you click on "Finish".
                    </div>
                )}
                To change content versions later, access the Content Manager from Device
                Resources.
            </p>
        );
        if (missingRequiredContent.length === 0) {
            contentStatus = "ALL_CONTENT_DOWNLOADED";
            return AllRequiredContentLocallyHeader;
        } else if (missingRequiredContent.length === requiredContent.length) {
            contentStatus = "NO_CONTENT_DOWNLOADED";
            return NoRequiredContentLocallyHeader;
        } else {
            contentStatus = "SOME_CONTENT_DOWNLOADED";
            return SomeRequiredContentLocallyHeader;
        }
    };

    const downloadStatusReqContentHeader = (): JSX.Element => {
        return (
            <div className="download-progress">
                Content download is in progress.
                <ProgressSpinner style={{ height: "28px", width: "37px", top: "8px" }} />
                Please expand the table rows to check the individual download status.
            </div>
        );
    };

    const createRequiredTableHeader = (): JSX.Element => {
        return <h2 className={"row justify-content-center"}>Required Content</h2>;
    };

    function getWizardStep(step: WizardSteps): JSX.Element {
        const totalSteps = 2;

        const requiredTableHeader: JSX.Element = getRequiredTableHeader();
        const requiredTablePageHeader: JSX.Element = createRequiredTableHeader();
        if (step === "CONTENT_SELECTOR") {
            return (
                <div className={"wizard-container"}>
                    <Nav
                        totalSteps={totalSteps}
                        currentStep={1}
                        nextStep={(): void => {
                            dispatch(actions.setWizardStep("TABLE"));
                        }}
                        goToStep={(step): { payload: WizardSteps; type: string } =>
                            dispatch(
                                actions.setWizardStep(
                                    step === 1 ? "CONTENT_SELECTOR" : "TABLE",
                                ),
                            )
                        }
                    />
                    <ContentSelector
                        supportedContentTypes={state.supportedContentTypes}
                        setContentType={(contentType): void => {
                            dispatch(actions.setContentType(contentType));
                        }}
                        catalogData={props.catalogData}
                    />
                </div>
            );
        } else if (step === "TABLE") {
            return (
                <div className={"wizard-container container-fluid"}>
                    <Nav
                        totalSteps={totalSteps}
                        currentStep={2}
                        nextStep={props.onFinished}
                        goToStep={(step): void => {
                            dispatch(
                                actions.setWizardStep(
                                    step === 1 &&
                                        contentStatus !== "SOME_CONTENT_DOWNLOADED"
                                        ? "CONTENT_SELECTOR"
                                        : "TABLE",
                                ),
                            );
                        }}
                        previousStep={(): void => {
                            dispatch(actions.setWizardStep("CONTENT_SELECTOR"));
                        }}
                        inProgress={props.inProgress}
                    />
                    {requiredTablePageHeader}
                    {requiredTableHeader}
                    {missingRequiredContent.length > 0 && (
                        <Accordion activeIndex={0}>
                            <AccordionTab header={"Required Content"}>
                                <Table
                                    tableData={missingRequiredContent}
                                    showProgress={true}
                                />
                            </AccordionTab>
                        </Accordion>
                    )}
                    <h2>Optional Content</h2>
                    <p>
                        Select optional content to be made available in Device Resources
                        for selection
                    </p>
                    <Accordion activeIndex={0}>
                        <AccordionTab header={"Optional Content"}>
                            <Table
                                tableData={optionalContent}
                                expandKeys={optionalContent}
                                showProgress={false}
                                selection={selection}
                                selectionMode={"checkbox"}
                                setSelection={(e): void => {
                                    setSelection(e.value);
                                }}
                                onSelect={(node): void => {
                                    const selectedNodes = getLeaves(node);
                                    if (state.contentType) {
                                        const newContent: ContentId[] = [
                                            ...selectedVersions[state.contentType],
                                            ...selectedNodes.map((node) => {
                                                return {
                                                    name: node.data?.id ?? "Unknown",
                                                    version: node.data?.moduleVersion,
                                                };
                                            }),
                                        ];
                                        props.onOptionalSelect(newContent);
                                    }
                                }}
                                onUnselect={(node): void => {
                                    if (state.contentType) {
                                        const newContent = selectedVersions[
                                            state.contentType
                                        ].filter((id) => {
                                            return id.name !== node.data?.id;
                                        });
                                        props.onOptionalSelect(newContent);
                                    }
                                }}
                            />
                        </AccordionTab>
                    </Accordion>
                </div>
            );
        } else {
            return <DeviceNotSupported />;
        }
    }

    return (
        <div>
            <span>
                <span className="settings-container">
                    <HelpIcon
                        onClick={(): void => {
                            window.open(CONTENT_MANAGER_TECHNICAL_HELP_LINK, "_blank");
                        }}
                        className={"technical-help"}
                        altText={"Technical Reference"}
                    />
                    <i
                        className={"pi pi-cog"}
                        onClick={(): void => {
                            setShowSettings(true);
                        }}
                    />
                </span>
                <h1 className={"centered"}>MCC Content Manager Wizard</h1>
            </span>
            <div>{getWizardStep(state.wizardStep)}</div>
            <Settings setShow={setShowSettings} showSettings={showSettings} />
        </div>
    );
};

export default Wizard;
