import { Button } from "primereact/button";
import { ProgressSpinner } from "primereact/progressspinner";
import { Toolbar } from "primereact/toolbar";
import React from "react";
import * as Hooks from "../../../../redux/hooks";
import { actions, getState } from "../../contentManagerSlice";
import FeatureGlobalFilter from "../globalFilter/FeatureGlobalSearch";
import styles from "./scss/header.module.scss";
import "./scss/primereact-override.scss";
import { Settings } from "../../../settings/Settings";
import { CONTENT_MANAGER_TECHNICAL_HELP_LINK } from "../../../common/common";
import { HelpIcon } from "../../../common/HelpIcon";
import { RootState } from "../../../../redux/CMTStore";

export interface HeaderProps {
    globalFilter: string[];
    setGlobalFilter: (filter: string[]) => void;
    onApply: () => void;
    onCancel: () => void;
    onSelectLatest: () => void;
    inProgress: boolean;
}

const Header: React.FC<HeaderProps> = (props): JSX.Element => {
    const [globalFilter, setGlobalFilter] = [props.globalFilter, props.setGlobalFilter];
    const [showSettings, setShowSettings] = React.useState(false);
    const state = getState(Hooks.useAppSelector((state: RootState): RootState => state));
    const dispatch = Hooks.useAppDispatch();

    return (
        <Toolbar
            className={styles.toolbarComponent}
            right={(): JSX.Element => (
                <div className={"apply-right-bar"}>
                    <>
                        {props.inProgress && (
                            <ProgressSpinner style={{ height: "30px" }} />
                        )}
                    </>
                    <Button
                        icon="pi pi-check-square"
                        className="p-mr-2 header-button"
                        label="Select Latest Version(s)"
                        onClick={(): void => {
                            props.onSelectLatest();
                        }}
                    />
                    <Button
                        icon="pi pi-check"
                        className="p-button p-button-primary p-mr-2 header-button"
                        label="Apply"
                        onClick={(): void => {
                            props.onApply();
                        }}
                        loading={state.isApplying}
                        disabled={state.selectedVersionsDiff.length === 0}
                    />
                    <Button
                        icon="pi pi-times"
                        className="p-button p-button-primary p-mr-2 header-button"
                        label="Cancel"
                        onClick={(): void => {
                            dispatch(actions.setSelectedVersions([]));
                        }}
                        loading={state.isApplying}
                        disabled={state.selectedVersionsDiff.length === 0}
                    />
                    <div className={styles.settingsContainer}>
                        <HelpIcon
                            onClick={(): void => {
                                window.open(
                                    CONTENT_MANAGER_TECHNICAL_HELP_LINK,
                                    "_blank",
                                );
                            }}
                            className={"technical-help"}
                            altText={"Technical Reference"}
                        />
                        <i
                            className={"pi pi-cog"}
                            onClick={(): void => {
                                setShowSettings(true);
                            }}
                        />
                    </div>
                </div>
            )}
            left={(): JSX.Element => (
                <div
                    title={
                        "Show content supported on the device architecture and content type"
                    }
                >
                    <FeatureGlobalFilter
                        globalSearchedTerms={globalFilter}
                        setGlobalSearchedTerms={setGlobalFilter}
                    />
                    <Settings setShow={setShowSettings} showSettings={showSettings} />
                </div>
            )}
        />
    );
};

export default Header;
