import { createSlice } from "@reduxjs/toolkit";
import type { PayloadAction } from "@reduxjs/toolkit";
import type { RootState } from "../../redux/CMTStore";

export type jobDescription = {
    id: number;
    type: string;
    data: PackageData;
    expectedTasks: number;
    finishedTasks: number;
};

export type taskDescription = {
    job: jobDescription;
    taskName: string;
    seq: number;
    isFinished: boolean;
    progressPercentage: number;
};

export type Task = {
    name: string;
    seq: number;
    progress: number;
    isFinished: boolean;
};

export type PackageData = {
    package: string;
    repo?: string;
    branch?: string;
};

export type Job = {
    id: number;
    type: string;
    data: PackageData;
    tasks: Task[];
    activeTask: Task | undefined;
    isFinished: boolean;
};

export type JobsState = {
    allJobs: Job[];
};

const initialState: JobsState = {
    allJobs: [],
};

export const slice = createSlice({
    name: "jobs",
    initialState: initialState,
    reducers: {
        reportJobStarted: (state, action: PayloadAction<jobDescription>) => {
            state.allJobs.push({
                id: action.payload.id,
                type: action.payload.type,
                data: action.payload.data,
                tasks: [],
                activeTask: undefined,
                isFinished: false,
            });
        },
        reportJobFinished: (state, action: PayloadAction<jobDescription>) => {
            const job = getJob(state, action.payload.id);
            if (undefined !== job) {
                job.isFinished = true;
            }
        },
        reportTaskStarted: (state, action: PayloadAction<taskDescription>) => {
            const job = getJob(state, action.payload.job.id);
            if (undefined !== job) {
                const task: Task = {
                    name: action.payload.taskName,
                    progress: 0,
                    isFinished: false,
                    seq: action.payload.seq,
                };
                job.tasks.push(task);
                job.activeTask = task;
            }
        },
        reportTaskProgress: (state, action: PayloadAction<taskDescription>) => {
            const job = getJob(state, action.payload.job.id);
            if (undefined !== job) {
                const task = getTask(job, action.payload.taskName);
                if (
                    undefined !== task &&
                    !task.isFinished &&
                    action.payload.seq > task.seq
                ) {
                    task.progress = action.payload.progressPercentage;
                    task.seq = action.payload.seq;
                    job.activeTask = task;
                }
            }
        },
        reportTaskFinished: (state, action: PayloadAction<taskDescription>) => {
            const job = getJob(state, action.payload.job.id);
            if (undefined !== job) {
                const task = getTask(job, action.payload.taskName);
                if (undefined !== task) {
                    task.isFinished = true;
                    task.progress = 100;
                    job.activeTask = task;
                }
            }
        },
        clearAllJobs: (state) => {
            state.allJobs = [];
        },
    },
    extraReducers: {},
});

function getJob(state: JobsState, id: number): Job | undefined {
    return state.allJobs.find((job) => job.id === id);
}

function getTask(job: Job, name: string): Task | undefined {
    return job.tasks.find((task) => task.name === name);
}

export type ReducerActions = typeof slice.actions;
export type ReducerState = ReturnType<typeof slice.reducer>;

export const actions = {
    ...slice.actions,
};

export const getState = (state: RootState): ReducerState => state.jobs;

export const reducer = slice.reducer;
