import { Button } from "primereact/button";
import React from "react";
import { assertNotNull } from "../../../model/types/Utils";
import "./scss/nav.scss";

export type NavProps = {
    totalSteps: number;
    currentStep: number;
    previousStep?: () => void;
    nextStep: () => void;
    goToStep?: (step: number) => void;
    inProgress?: boolean;
    stepTitles?: string[];
};

const Nav: React.FC<NavProps> = (props): JSX.Element => {
    const steps = [];
    const stepTitles = props.stepTitles ?? ["Content Type", "Required Device Content"];
    assertNotNull(props.totalSteps, "Total setup steps cannot be null or undefined");
    assertNotNull(props.currentStep, "Current setup step cannot be null or undefined");
    if (!props.goToStep) return <></>;

    for (let i = 1; i <= props.totalSteps; i++) {
        const isActive = props.currentStep === i;
        const isPreviousStep = i <= props.currentStep;
        const className = isActive
            ? "active text-decoration-underline"
            : isPreviousStep
            ? "active"
            : "inactive";

        steps.push(
            <div
                key={`container-${i}`}
                className={`step-container ${className} `}
                onClick={(): void => onPageChange(props.currentStep, i)}
            >
                <span key={`step-${i}`} className={`tab ${className}`}>
                    {i}. {stepTitles[i - 1]}
                </span>
            </div>,
        );
    }

    const onPageChange = (currentStep: number, newStep: number): void => {
        if (newStep <= currentStep) {
            props.goToStep?.(newStep);
        }
    };

    function getFinishedButton(): JSX.Element {
        return (
            <>
                <Button
                    className={`p-button p-button-primary p-button-sm p-mr-2 nav-button step-button finish-button active`}
                    onClick={(): void => {
                        props.nextStep?.();
                    }}
                    label="Finish"
                    loading={props.inProgress}
                />
            </>
        );
    }

    return (
        <div className={"container"}>
            <div className={"nav-row row"}>
                {steps}
                {props.currentStep >= props.totalSteps && getFinishedButton()}
            </div>
            <hr />
        </div>
    );
};

export default Nav;
