import { Content } from "../BaseContent/Content";
import type { Meta } from "../BaseContent/Manifest";
import type { RowData } from "../types/RowData";
import type { MelodyManifest } from "./MelodyManifest";

export class MelodyContent extends Content<MelodyManifest> {
    public getRowData = (manifest: MelodyManifest): RowData => {
        return {
            id: this.id,
            moduleName: manifest.meta?.name ?? manifest.scf?.displayName ?? manifest.name,
            moduleVersion: manifest.version,
            moduleDescription: manifest.meta?.description ?? "",
            moduleCategory: manifest.meta?.category ?? manifest.scf?.category,
            packageName: manifest.name,
            help: manifest.scf?.help,
            versions: manifest.versionStatusMap,
            status: this.getSelectedStatus(),
            versionReadMeUrlMap: manifest.versionReadMeUrlMap,
            meta: injectMeta(manifest),
            compatibilityErrors: manifest.versionCompatibilityErrorMap,
        };
    };
}

function injectMeta(manifest: MelodyManifest): Meta | undefined {
    if (manifest?.meta) {
        return {
            ...manifest.meta,
            keywords: [...manifest.meta.keywords, "melody"],
        };
    }
    return {
        name: manifest.scf?.displayName ?? manifest.name,
        category: manifest.scf?.category ?? "",
        description:
            (manifest.meta?.name ?? manifest.scf?.displayName ?? manifest.name) +
            ` Description`,
        keywords: ["melody"],
    };
}
