import React from "react";
import * as Hooks from "../../redux/hooks";
import { Dialog } from "primereact/dialog";
import { ProgressBar } from "primereact/progressbar";
import "./loadingScreen.scss";

const LoadingScreen = (): JSX.Element => {
    const state = Hooks.useAppSelector((state) => state.loadingScreen);
    const { totalSteps, currentStep, currentMessage, isVisible } = state;
    const [start] = React.useState(new Date());
    const [time, setTime] = React.useState(0);

    React.useEffect(() => {
        const interval = setInterval(() => {
            setTime(new Date().getTime() - start.getTime());
        }, 10);
        return (): void => clearInterval(interval);
    });

    const getCurrentTime = (): string => {
        const minutes = Math.floor((time / 60000) % 60);
        const seconds = Math.floor((time / 1000) % 60);
        const milliseconds = Math.floor((time % 1000) / 100);

        const minutesString = convertTimeValueToString(minutes);
        const secondsString = convertTimeValueToString(seconds);
        const millisecondsString = convertTimeValueToString(milliseconds);

        return `${minutesString}:${secondsString}.${millisecondsString}`;
    };

    const convertTimeValueToString = (value: number): string => {
        return value < 10 ? "0" + value.toString() : value.toString();
    };

    const getCurrentProgress = (): number => {
        const currentProgress = (currentStep / totalSteps) * 100;
        return Math.floor(currentProgress);
    };

    return (
        <Dialog
            visible={isVisible && currentStep <= totalSteps}
            closable={false}
            showHeader={false}
            style={{ width: "60vw" }}
            onHide={(): void => console.log("Loading screen closed")}
        >
            <div className="d-flex mb-3">
                <h3 className="ms-auto">Loading MCC Content Manager Wizard</h3>
                <div className="ms-auto d-flex">
                    <span className="pi pi-clock me-1 mt-1" />
                    <span className="p-1">{getCurrentTime()}</span>
                </div>
            </div>

            <div className="d-flex justify-content-between">
                <h6 className="fw-bold">{currentMessage}</h6>
                <span className="text-muted">
                    Step {currentStep} of {totalSteps}
                </span>
            </div>

            <ProgressBar value={getCurrentProgress()}></ProgressBar>
        </Dialog>
    );
};

export default LoadingScreen;
