import React from "react";
import { Button } from "primereact/button";
import { Dialog } from "primereact/dialog";
import { TabMenu } from "primereact/tabmenu";
import SourceSettings from "./components/SourceSettings";
import ContentManagerUpdate from "./components/ContentManagerUpdate";
import MccFrontendUpgrade from "./components/MccFrontendUpgrade";
import "./scss/settingsModal.scss";

interface Props {
    showSettings: boolean;
    setShow: (show: boolean) => void;
}

export const Settings = (props: Props): JSX.Element => {
    const [activeIndex, setActiveIndex] = React.useState(0);

    const items = [
        { label: "Registries", icon: "pi pi-database" },
        {
            label: "Updates",
            icon: "pi pi-cloud-download",
        },
        {
            label: "",
            className: "d-flex w-100 secondary-tab",
            template: (): JSX.Element => {
                return <span style={{ width: "0px" }} />;
            },
        },
    ];

    const footer = (): JSX.Element => {
        return (
            <Button
                className={"p-button-danger d-flex ms-auto"}
                icon="pi pi-times"
                label="Close"
                onClick={(e): void => {
                    e.preventDefault();
                    props.setShow(false);
                }}
            />
        );
    };

    return (
        <Dialog
            header={"Settings"}
            footer={footer}
            className="settings-dialog"
            visible={props.showSettings}
            onHide={(): void => props.setShow(false)}
        >
            <TabMenu
                model={items}
                activeIndex={activeIndex}
                onTabChange={(e): void => setActiveIndex(e.index)}
            />
            <div className="container-fluid p-4 pt-2">
                {activeIndex === 0 && <SourceSettings />}
                {activeIndex === 1 && (
                    <>
                        <ContentManagerUpdate />
                        <MccFrontendUpgrade />
                    </>
                )}
            </div>
        </Dialog>
    );
};
