import React from "react";
import SearchTermInputTextBox from "./SearchTermInputBox";
import SearchTermsDisplay from "./SearchTermsDisplay";
import "./scss/primereact-override.scss";

interface FeatureGlobalFilterProps {
    globalSearchedTerms: string[];
    setGlobalSearchedTerms: (searchedTerms: string[]) => void;
}

const FeatureGlobalFilter: React.FC<FeatureGlobalFilterProps> = (props) => {
    return (
        <>
            <SearchTermInputTextBox
                onSearchTermEntered={(searchTerm): void =>
                    props.setGlobalSearchedTerms([
                        ...props.globalSearchedTerms,
                        searchTerm,
                    ])
                }
            />
            {props.globalSearchedTerms.length > 0 && (
                <SearchTermsDisplay
                    globalSearchedTerms={props.globalSearchedTerms}
                    setGlobalSearchedTerms={props.setGlobalSearchedTerms}
                />
            )}
        </>
    );
};

export default FeatureGlobalFilter;
