import type TreeNode from "primereact/treenode/treenode";
import { getCategory as defaultCategory } from "../BaseContent/CategorySort";
import type { RowData } from "./RowData";
import type { ModuleStatus } from "./RowData";
import type { SelectedNodes } from "../utils/selectionUtil";
import type { DefaultSelection } from "@mchp-mcc/scf-catalog/lib/src/ContentEntry";
import type { ExpandedKeys } from "../../features/types";

export interface MyTreeNode extends TreeNode {
    data?: RowData;
    children: MyTreeNode[];
}

export function isMyTreeNode(node: MyTreeNode | TreeNode): node is MyTreeNode {
    return (node as MyTreeNode).data !== undefined;
}

export type CategoryInjector = (data: RowData) => string[];

export const addTreeNode = (
    nodes: MyTreeNode[],
    rowData: RowData,
    getCategory?: CategoryInjector,
): void => {
    const repoCategory = getCategory?.(rowData) ?? defaultCategory()(rowData);
    if (repoCategory.length === 0) {
        //add as root node
        nodes.push(
            getNewTreeNode(rowData.moduleName ?? "unknown", rowData.id, [], rowData),
        );
    } else {
        const categoryName = repoCategory[0].trim();

        let existingNode = nodes.find((node) => {
            return node.data?.moduleName === categoryName;
        });

        if (existingNode) {
            existingNode = getTreeNode(
                repoCategory.slice(1),
                categoryName,
                existingNode,
                rowData,
            );
        } else {
            const node = getTreeNode(
                repoCategory.slice(1),
                categoryName,
                getNewTreeNode(categoryName),
                rowData,
            );
            nodes.push(node);
        }
    }
};

export const getTreeNode = (
    depth: string[],
    category: string,
    myTreeNode: MyTreeNode,
    data: RowData,
): MyTreeNode => {
    const name = depth[0];
    const nextCategory = category + "/" + name;
    const existingNode = myTreeNode.children.find((node) => {
        return node.data?.moduleName === depth[0];
    });
    if (depth.length > 1) {
        if (existingNode) {
            getTreeNode(depth.slice(1), nextCategory, existingNode, data);
        } else {
            myTreeNode.children.push(
                getTreeNode(
                    depth.slice(1),
                    nextCategory,
                    getNewTreeNode(name, nextCategory),
                    data,
                ),
            );
        }
        return myTreeNode;
    }
    if (existingNode) {
        myTreeNode = getNewTreeNode(name, nextCategory, undefined, data);
    } else {
        const newNode = getNewTreeNode(name ?? data.id, nextCategory, undefined, data);
        myTreeNode.children.push(newNode);
    }

    return myTreeNode;
};

export const getNewTreeNode = (
    name: string,
    id?: string,
    children?: MyTreeNode[],
    data?: RowData,
): MyTreeNode => {
    const key = data?.id ?? data?.moduleName ?? name;
    return {
        children: children ?? [],
        data: {
            ...data,
            moduleName: name,
        },
        key: data ? key : id ?? key,
    };
};

export const populateStatus = (nodes: MyTreeNode[]): ModuleStatus | undefined => {
    return getStatusFromChildren(nodes);
};

const getStatusFromChildren = (nodes: MyTreeNode[]): ModuleStatus | undefined => {
    let status: ModuleStatus | undefined = undefined;
    nodes.forEach((node) => {
        let nodeStatus: ModuleStatus | undefined;
        if (node.children.length > 0) {
            nodeStatus = getStatusFromChildren(node.children) ?? nodeStatus;
        }

        if (node.data?.moduleVersion && node.data.status === "AVAILABLE") {
            //Found existing DOWNLOAD
            nodeStatus = node.data.status;
        } else if (node.data && !node.data?.moduleVersion && nodeStatus === "AVAILABLE") {
            node.data.status = nodeStatus;
        } else if (node.data?.moduleVersion && node.data.status === "UPDATE") {
            //Found existing UPDATE
            nodeStatus = node.data.status;
        } else if (node.data && !node.data?.moduleVersion && nodeStatus === "UPDATE") {
            node.data.status = nodeStatus;
        }

        if (nodeStatus === "UPDATE") {
            status = "UPDATE";
        } else if (nodeStatus === "AVAILABLE") {
            status = "AVAILABLE";
        }
    });
    return status;
};

function isNodeDefaultRequired(node: MyTreeNode): boolean {
    const defaultSelection: DefaultSelection | boolean | string | undefined =
        node.data?.meta?.defaultSelection;
    return defaultSelection === "defaultRequired" || defaultSelection === "true";
}
function isNodeDefaultSelected(node: MyTreeNode): boolean {
    const defaultSelection: DefaultSelection | boolean | string | undefined =
        node.data?.meta?.defaultSelection;
    return defaultSelection === "defaultSelected";
}
export function isNodeDefault(node: MyTreeNode): boolean {
    return isNodeDefaultRequired(node) || isNodeDefaultSelected(node);
}

export function getDefaultSelections(
    tableData: MyTreeNode[],
    selectedNodes: SelectedNodes,
): SelectedNodes {
    tableData.forEach((node) => {
        if (node.key && !selectedNodes[node.key]) {
            if (
                isNodeDefault(node) ||
                areAllChildNodesDefaultSelected(node, isNodeDefault)
            ) {
                selectedNodes[node.key] = {
                    checked: true,
                    partialChecked: false,
                };
            } else if (
                isNodeDefault(node) ||
                areSomeChildNodesDefaultSelected(node, isNodeDefault)
            ) {
                selectedNodes[node.key] = {
                    checked: false,
                    partialChecked: true,
                };
            }
        }

        if (node.children.length > 0) {
            getDefaultSelections(node.children, selectedNodes);
        }
    });
    return selectedNodes;
}
export function getDefaultRequiredNodes(
    tableData: MyTreeNode[],
    requiredNodes: MyTreeNode[] = [],
): MyTreeNode[] {
    tableData.forEach((node) => {
        if (node.key && isNodeDefaultRequired(node)) {
            requiredNodes.push(node);
        }

        if (node.children.length > 0) {
            getDefaultRequiredNodes(node.children, requiredNodes);
        }
    });
    return requiredNodes;
}

export function getOptionalNodes(
    tableData: MyTreeNode[],
    optionalNodes: MyTreeNode[] = [],
): MyTreeNode[] {
    tableData.forEach((node) => {
        if (node.key && !isNodeDefaultRequired(node) && node.children.length === 0) {
            optionalNodes.push(node);
        }

        if (node.children.length > 0) {
            getOptionalNodes(node.children, optionalNodes);
        }
    });
    return optionalNodes;
}

export function areAllChildNodesDefaultSelected(
    data: MyTreeNode,
    isDefault: (node: MyTreeNode) => boolean,
): boolean {
    return (
        data.children.length > 0 &&
        data.children.every((child) => {
            return determineDefaultNodesAccordingToStrategy(
                child,
                isDefault,
                areAllChildNodesDefaultSelected,
            );
        })
    );
}

export function areSomeChildNodesDefaultSelected(
    data: MyTreeNode,
    isDefault: (node: MyTreeNode) => boolean,
): boolean {
    return (
        data.children.length > 0 &&
        data.children.some((child) => {
            return determineDefaultNodesAccordingToStrategy(
                child,
                isDefault,
                areSomeChildNodesDefaultSelected,
            );
        })
    );
}
function determineDefaultNodesAccordingToStrategy(
    node: MyTreeNode,
    isDefault: (node: MyTreeNode) => boolean,
    defaultSelectedQuantStategy: (
        node: MyTreeNode,
        isDefault: (node: MyTreeNode) => boolean,
    ) => boolean,
): boolean {
    const childrenCount = node.children.length;
    if (!isDefault(node) && childrenCount === 0) {
        return false;
    }
    if (childrenCount > 0) {
        return defaultSelectedQuantStategy(node, isDefault);
    }
    return true;
}
export function setDefaultRequiredToUnselectable(node: MyTreeNode): MyTreeNode {
    if (node.children.length <= 0 && isNodeDefaultRequired(node)) {
        node.selectable = false;
    }
    node.children.forEach((child) => {
        setDefaultRequiredToUnselectable(child);
    });
    if (areAllChildNodesDefaultSelected(node, isNodeDefaultRequired)) {
        node.selectable = false;
    }
    return node;
}
export function makeDefaultRequiredUnselectable(tableData: MyTreeNode[]): MyTreeNode[] {
    const modifiedTableData: MyTreeNode[] = [];
    tableData.forEach((node) => {
        modifiedTableData.push(setDefaultRequiredToUnselectable(node));
    });
    return modifiedTableData;
}

export function getLeaves(node: MyTreeNode, leafNodes: MyTreeNode[] = []): MyTreeNode[] {
    if (node.children && node.children.length > 0) {
        const children = node.children;
        children.forEach((child) => {
            return getLeaves(child, leafNodes);
        });
    } else {
        leafNodes.push(node);
    }
    return leafNodes;
}

export const getAllLeavesUnderCategory = (
    category: string,
    leaves: MyTreeNode[],
): MyTreeNode[] => {
    return leaves.filter((leaf) => {
        return category !== "" && leaf.data?.moduleCategory === category;
    });
};

export const getAllKeysAsExpanded = (table: MyTreeNode[]): ExpandedKeys => {
    const expanded: ExpandedKeys = {};
    table.forEach((node) => {
        const leaves = getLeaves(node);
        leaves.forEach((leaf) => {
            const categories = leaf.data?.moduleCategory?.split("/");
            categories?.forEach((category) => {
                expanded[category] = true;
            });
        });
    });

    return expanded;
};

const isUncategorized = (node: MyTreeNode): boolean => {
    return !node.data?.moduleCategory || node.data?.moduleCategory === "";
};
export const hasParent = (node: MyTreeNode): boolean => {
    return !isUncategorized(node);
};
