import React from "react";
import { Button } from "primereact/button";
import { Dialog } from "primereact/dialog";

interface ConfirmProps {
    title: string;
    description: Array<string>;
    okButtonLabel?: string;
    cancelButtonLabel?: string;
    onOk: () => void;
    onCancel: () => void;
}

/**
 * Simple confirmation dialog with 'ok', 'cancel' or other custom options.
 */
export class ConfirmDialog extends React.Component<ConfirmProps> {
    render(): JSX.Element {
        const footer = (
            <div>
                <Button
                    label={this.props.okButtonLabel ?? "Ok"}
                    icon="pi pi-check"
                    onClick={(): void => {
                        this.props.onOk();
                    }}
                />
                <Button
                    label={this.props.cancelButtonLabel ?? "Cancel"}
                    icon="pi pi-times"
                    onClick={(): void => {
                        this.props.onCancel();
                    }}
                    className="p-button-secondary"
                />
            </div>
        );
        return (
            <div>
                <Dialog
                    header={this.props.title}
                    footer={footer}
                    modal={true}
                    visible={true}
                    onHide={(): void => this.props.onCancel()}
                    maximizable={false}
                >
                    {this.props.description.map((content) => (
                        <div key={content}>{content}</div>
                    ))}
                </Dialog>
            </div>
        );
    }
}
